<template>
  <div class="homepage">
    <el-row>
           <el-col :span="8" class="headerBox"  :xs="12">
            <img src="../assets/homepage/module_daiiguanli.png"/>
            <div class="headerText">
                <p class="headerNo">{{mngInfo.notMngedNum}}</p>
                <p class="headerName">待管理债务人</p>
            </div>
        </el-col>
        <el-col :span="8" class="headerBox" :xs="12">
            <img src="../assets/homepage/module_yiguanli.png"/>
            <div class="headerText">
                <p class="headerNo">{{mngInfo.hasMngedNum}}</p>
                <p class="headerName">已管理债务人</p>
            </div>
        </el-col>
     
        <el-col :span="8" class="headerBox" :xs="12">
            <img src="../assets/homepage/module_youfengxian-.png"/>
            <div class="headerText">
                <p class="headerNo">{{mngInfo.riskNum}}</p>
                <p class="headerName">债务人存在风险</p>
            </div>
        </el-col>
    </el-row>

    <el-row class="chartGroup">
        <el-col :span="12" class="whiteBg" :xs="24">
           <div class="chartTitle">
                <p>管理结果人数（周）</p>
            </div>
            <managed-acc-charts/>
        </el-col>
        <el-col :span="12" class=" chartRight" :xs="24">
            <div class="whiteBg">
                <div class="chartTitle">
                    <p>风险概览（结果）</p>
                </div>
                 <emergency-notice-charts  />
            </div>
            <div class="whiteBg marginTop">
                <div class="chartTitle ">
                    <p>风险趋势变化图</p>
                </div>
                <div class="chartBox" style="height: 190px;">
                    <risk-trendsed-acc-chart/>
                </div>
            </div>
        </el-col>
    </el-row>
    <p class="CopyRightNo">CopyRight &copy;2016-2018 律军风控 版权所有 蜀ICP备16016755号-7</p>
  </div>
</template>
<script>
import Vue from 'vue'

import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'
import echarts from 'echarts'
import managedAccCharts from './managedAccCharts'
import riskTrendsedAccChart from './riskTrendsedAccChart'
import emergencyNoticeCharts from './emergencyNoticeCharts'
@Component
@InjectComponents({managedAccCharts,riskTrendsedAccChart,emergencyNoticeCharts})
export default class HomePage extends Vue{
    mngInfo = {hasMngedNum:0,notMngedNum:0,riskNum:0}
      @Lifecycle async created() {
     const rsp = await this.$helper.fetch('/api/index/getCustMngGeneralData').send()
      if (!rsp.success) return rsp.success
      this.mngInfo = rsp.data.mngInfo;

  }
}



</script>
<style scoped>
p{margin: 0}
.homepage .el-row{margin: 15px 5px 0 5px}
.homepage{padding-bottom: 50px}
.homepage p{color: #fff;}
.homepage .headerBox{position: relative}
.homepage .headerBox img{width: 100%}
.homepage .headerBox .headerText{position: absolute;right: 30px;top: 0;color: #fff;text-align: center;width: 40%;margin-top: 6%;font-size: 20px}
.homepage .headerBox .headerNo{font-size: 40px;border-bottom: 1px solid #fff;line-height: 60px}
.homepage .headerBox .headerName{line-height: 48px}
.homepage .chartGroup{margin: 10px 20px 20px 20px;}
.homepage .whiteBg{background-color: #fff}
.homepage .chartTitle{color: #6b7074;line-height: 60px;padding-left: 16px;font-size: 14px;border-bottom: 1px solid #ecf0f1;}
.homepage .chartTitle p{color: #6b7074;}
.homepage .chartRight{padding-left: 30px}
.homepage .chartRight .chartBox{padding-bottom: 20px}
.homepage #managedAcc{padding-bottom: 40px}
.homepage .marginTop{margin-top: 20px}
.homepage .chartBox>div{display: inline-block;float: left;}
@media screen and (max-width: 1200px) {
    .homepage .headerBox .headerText{width: 42%}
   .homepage .headerBox .headerNo{font-size: 28px;line-height: 36px}
   .homepage .headerBox .headerName{font-size: 13px;line-height: 30px}
}
@media screen and (max-width: 860px) {
    .homepage .headerBox .headerText{width: 45%}
   .homepage .headerBox .headerNo{font-size: 22px;line-height: 32px}
   .homepage .headerBox .headerName{font-size: 12px;line-height: 26px}
}
@media screen and (max-width: 768px) {
    .homepage .headerBox .headerText{width: 45%}
   .homepage .chartRight{padding-left: 0;margin-top: 20px}
}

</style>


