<template>
  <div class="full">
    <template v-if="visible" >
        <!-- // 父组件 -->
        <cust-info ref="custInfo" @open="open"/>
    </template>
    <template v-if="!visible">
        <!-- // 子组件 -->
          <cust-choose  v-if ="isothervis == 'custChooseForm'"  ref="custChooseForm" @close="close" :autoload="false"/>
          <history-report   v-if ="isothervis == 'historyReport'" ref = "historyReport" @close="close" :autoload="false"/>

    </template>
  </div>
</template>

<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";

import custInfo from './custInfo'
import custChoose from './custChoose'
import historyReport from './historyReport'

@Component
@InjectComponents({custInfo,custChoose,historyReport})
export default class ModifyLoginPwdForm extends Vue{
    visible = true
       isothervis =null//显示那个
    //获取accmanage中的emit传过来的参数
    open(params) { 
       const {toManage} = params
      this.visible = false
      if(toManage =="toManage"){
          this.isothervis ="custChooseForm"
        this.$nextTick(() => {
                this.$refs.custChooseForm.setParams(params)
              })
      } else if(toManage =="historyReport"){
          this.isothervis ="historyReport"
          this.$nextTick(() => {
                this.$refs.historyReport.setParams(params)
              })
      }
    }

    close(data) {
        this.visible = true
        //this.$refs.accManage.load(data)
    }


}
</script>




