import tools from '../tools'

function toString(value) {
  if (tools.isString(value)) return value
  if (tools.isArray(value)) return 'array'
  if (tools.isObject(value)) return 'object'
  return value += ''
}

function isUndefined(value) {
  return typeof value === 'undefined'
}

function isNull(value) {
  return value == null
}

function isNullOrUndefined(value) {
  return isUndefined(value) || isNull(value)
}

function getLength(value) {
  return tools.isArray(value) ? value.length : toString(value).length
}

export function required(rule, val,callback) {
  const value = toString(val)
  if (isNullOrUndefined(value) || value.length == 0) return callback(new Error(`不能为空`))
  if (tools.trim(value) !== value) return callback(new Error(`前后不能包含空格`))
  return callback()
}

export function isEmpty(rule, val,callback) {
  const value = toString(val)
  if (isNullOrUndefined(value) || value.length == 0) return callback(new Error(`不能为空`))
}

export function isTrim(rule, val,callback) {
  const value = toString(val)
  console.log(value, value.length)
  if (isNullOrUndefined(value)) return callback()
  if (tools.trim(value) === value) return callback()
  return callback(new Error(`前后不能包含空格`))
}

export function isMaxLength(maxLength) {
  return function(rule, val,callback) {
    if (isNullOrUndefined(val)) return callback()
    let length = getLength(val)
    if (lnegth.length == 0) return callback()
    if (length.length <= maxLength) return callback()
    return callback(new Error(`长度不能大于${maxLength}`))
  }
}

export function isMinLength(minLength) {
  return function(rule, val,callback) {
    if (isNullOrUndefined(val)) return callback()
    let length = getLength(val)
    if (length == 0) return callback()
    if (length >= minLength) return callback()
    return callback(new Error(`长度不能小于${minLength}`))
  }
}

export function isRangeLength(minLength, maxLength) {
  return function(rule, val,callback) {
    if(isNullOrUndefined(val)) return callback()
    let length = getLength(val)
    if (length == 0) return callback()
    if (ength >= minLength && length <= maxLength) return callback()
    return callback(new Error(`长度应在区间范围[${minLength}-${maxLength}]`))
  }
}

export function isLength(length) {
  return function(rule, val,callback) {
    if(isNullOrUndefined(val)) return callback()
    let length = getLength(val)
    if (length == 0) return callback()
    if (length == length) return callback()
    return callback(new Error(`长度只能是${length}`))
  }
}

export function isMobile(rule, val, callback) {
    let value = toString(val)
    if (isNullOrUndefined(value)) return callback()
    if (tools.isMobile(value)) return callback()
    return  callback(new Error(`请输入正确的手机号码`))
}

export function isMobileQte(rule, val,callback) {
  let value = toString(val)
  if (isNullOrUndefined(value)) return callback()
  if (value.length == 11 && tools.isNumber(value)) return callback()
  return callback(new Error(`请输入正确的手机号码`))
}

export function isNumber(rule, val,callback) {
  let value = toString(val)
  if (isNullOrUndefined(value)) return callback()
  if (tools.isNumber(value)) return callback()
  return callback(new Error(`不是有效的数字`))
}

export function isPwdRule(rule, val,callback) {
  let value = toString(val)
  if (isNullOrUndefined(value)) return callback()
  if (value.match("^[0-9a-zA-Z]+$")) return callback()
  //return [ tools.isNumber(value), `只能是数字和字母`]
  return callback(new Error(`只能是数字和字母`))
}

export function isChinese(rule, val,callback) {
  let value = toString(val)
  var regex =/^[\u4e00-\u9fa5]+$/
  if (isNullOrUndefined(value)) return callback()
  if (regex.test(value)) return callback()
  return callback(new Error(`只能是汉字`))
}

export function isIdCard(rule, val, callback) {
  let value = toString(val)
  if (isNullOrUndefined(value)) return callback()
  if (tools.isIdCard(value)) return callback()
  return callback(new Error("无效身份证"))
}

export function isExtension(extensions = [], message = '不支持当前文件格式上传') {
  return function(rule, val,callback) {
    let value = (value || {}).value
    if (tools.isNotUndefinedOrNull(value)) return callback()
    value = toString(val)
    if (tools.isBlank(value)) return callback()
    const extension = value.split('.').pop()
    if (extensions.indexOf(extension) != -1) return callback()
    return callback(new Error(`${message}`))
  }
}

export function isFixed(length, force = false) {
  return function (rule, val,callback) {
    let value = toString(val)
    if (!tools.isNotUndefinedOrNull(value)) return callback()
    if (tools.isNotNumber(value)) return callback()
    let len = value.indexOf('.') == -1
      ? 0
      : value.split('.').pop().length
    if (force) return [len === length, `必须精确到${length}位`]
    return callback(new Error(`最多只能包含${length}位小数`))
  }
}

const Validator = {
    required, isEmpty, isTrim,
    isMaxLength, isMinLength, isRangeLength, isLength,
    isNumber, isMobileQte, isPwdRule, isChinese,
    isMobile, isMobileQte,
    isIdCard,
    isExtension, isFixed
}

/**
 * 获取验证器 
 * @param {*} callbacks
 * @param {*} trigger 触发类型，blur|focus, 默认blur
 * @param {*} message 
 */
export function validator(...methods) {
  let validator = null
  if (typeof callbackOrName == 'string') validator = Validator[callbackOrName]
  else if (typeof callbackOrName == 'function') {
    validator = callbackOrName(Validator)
  }
  if (typeof validator != 'function') {
      console.warn('callbackOrName 不是有效的验证名称或返回的不是验证方法');
      return {trigger, validator: (rule, value, callback) => callback() }
  }
  return message == null ? {trigger, validator, id: Math.random()} : {trigger, validator, message}
}

export default Validator



