import {Module, State, Action, Getter, Mutation} from 'vue-decorators';

@Module({namespaced: true})
export default class LayoutModule {
  @State innerWidth = 0
  @State innerHeight = 0

  @State running = false

  @Mutation setLayout(state, {innerWidth = 0, innerHeight = 0} = {}) {
    state.innerHeight = innerHeight
    state.innerWidth = innerWidth
  }

  @Mutation setRunning(state, running = true) {
    state.running = running
  }

  @Action updateLayout({commit}) {
    const innerWidth = window.innerWidth
    const innerHeight = window.innerHeight
    commit('setLayout', {innerWidth, innerHeight});
  }

  @Action run({dispatch, commit, state}) {
    if (state.running) return
    commit('setRunning', true)
    window.onresize = () => dispatch('updateLayout')
    dispatch('updateLayout')
  }
}



