<template>
  <div class="roleManage contentBox">
    <div class="breadCrumb">
   <el-breadcrumb separator="》">
      <el-breadcrumb-item>账户管理</el-breadcrumb-item>
      <el-breadcrumb-item >帐号管理</el-breadcrumb-item>
      <el-breadcrumb-item>客户转移</el-breadcrumb-item>
    </el-breadcrumb>
    </div>

    <div class="container whiteBg">
      <self-grid ref="grid" url="/api/custMgr/queryCustList"  v-model="queryForm" :autoload="autoload">
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline" :inline="true" :model="form">
            <el-form-item label="姓名：">
              <el-input v-model="form.custName"></el-input>
            </el-form-item>
            <el-form-item label="身份证号码：">
              <el-input  v-model="form.custCertNo"></el-input>
            </el-form-item>
            <el-form-item label="状态：">
            <el-select v-model="form.mngStatus" clearable placeholder="请选择">
              <el-option v-for="(item, index) in codeStorage.dataSource('mngStatus')" :key="index" :label="item.label" :value="item.value"/>
           </el-select>
            </el-form-item>
            <el-form-item class="buttonTeam">
              <el-button type="primary" @click="query">查询</el-button>
              
              <!-- <el-button type="danger resetEditClass" v-if="authStorage.isRole('admin')" @click="reset">重置</el-button> -->
              <el-button type="success" @click="custMove">客户转移</el-button>
               <el-button type="warning" @click="() => $emit('close','11111')">返回</el-button>
            </el-form-item>
          </el-form>
        </template>
        
        <el-table-column prop="custName" label="姓名"/>
        <el-table-column prop="custCertNo" label="身份证"/>
        <el-table-column prop="custTel" label="手机号"/>
         <el-table-column prop="loanAmt" label="借款金额"/>
        <el-table-column prop="mngStatus" label="状态">
             <template slot-scope="{row}">{{codeStorage.get('mngStatus', row.mngStatus)}}</template>
        </el-table-column>
      </self-grid>
    </div>

    <self-dialog ref="custMoveForm" title="客户转移" v-model="userMoveFormVisible" @close="closeuserMoveForm">
      <template slot-scope="{close, params}">
        <cust-move-form :close="close" :params="params"/>
      </template>
    </self-dialog>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";

import addUserForm from './addUserForm'
import editUserForm from './editUserForm'
import custMoveForm from './custMoveForm'

//码表
require('@/store/cache').cacheCodes('mngStatus')
@Component
@InjectComponents({ BreadCrumb,custMoveForm})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
  @Prop({type: Boolean, required: false, default: true}) autoload

  queryForm = {}
  getparamsFrom = {}
  userMoveFormVisible = false;

  form = {userId:'',loginId: '', userName: ''}

  setParams(params = {}) {//从CustMoveWrapper 的custMoveForm中获取的参数
    const {userId = ''} = params
    this.queryForm = {...this.queryForm,userId}
    this.$refs.grid.setDefaultParams({userId}).load()//内置一个值
  } 

  //客户转移
  custMove(){
    const rows = this.$refs.grid.selectedRows
    if(rows.length ==0) 
      return this.$notify({title: '提示',message:  '请至少选择一条数据',type: 'error',duration:4000});
    const userId = this.queryForm.userId //获取父页面的userId
    this.userMoveFormVisible =  true
    const params = {rows,userId}
    this.$refs.custMoveForm.setParams({...params})
  }

  closeuserMoveForm(){
     this.$refs.grid.load({"pageNo":1});//重新加载列表
  }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
.breadCrumb{background-color: #fff;padding-left: 20px;border-bottom: 1px solid #ecf0f1}
.breadCrumb .el-breadcrumb{font-size: 14px;line-height: 42px;}   
</style>


