import Vue from 'vue'
import Router from 'vue-router'
import beforeEach from '@/router/beforeEach'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
      { path: '/login', name: 'login', component: require('@web/page/Login').default },
      { path: "/html", component: require('@web/components/Layout').default, children: [
        { path: "index", name: "index", component: require('@web/page/Homepage').default },

        //{path: '/homepage',component: require('@web/page/Homepage').default, meta: ['首页', '执行日志']},
        // {path: 'account', name: "account", component: require('@web/page/accManage').default,meta: ['账户管理', '帐号管理']},
         {path: 'account', name: "account", component: require('@web/page/CustMoveWrapper').default,meta: ['账户管理', '帐号管理']},

        {path: 'custMove', name: "custMove" ,component: require('@web/page/custMove').default,meta: ['账户管理', '帐号管理','客户转移']},
        {path: 'custInput', name: "custInput" ,component: require('@web/page/custInput').default,meta: ['信息录入', '借款人录入']},
        
        {path: 'custinfo', name: "custinfo" ,component: require('@web/page/ServiceManageWrapper').default,meta: ['业务管理', '借款人信息']},
        // {path: 'accCust', name: "accCust" ,component: require('@web/page/manageAccount').default,meta: ['业务管理', '管理客户']},
         {path: 'accCust', name: "accCust" ,component: require('@web/page/manageAccountWrapper').default,meta: ['业务管理', '管理借款人']},

        {path: 'dangerCount', name: "dangerCount" ,component: require('@web/page/dangerCountWrapper').default,meta: ['查看报告', '风险统计']},
        {path: 'dangerReport', name: "dangerReport" ,component: require('@web/page/dangerReport').default,meta: ['查看报告', '风险报告']},        
        {path: 'myBill', name: "myBill" ,component: require('@web/page/myBill').default,meta: ['账单管理', '我的账单']},

        { path: "unauthorized", name: "unauthorized", component: require('@web/page/Unauthorized').default },
        { path: "*", name: "404", component: require('@web/page/NotFound').default }
      ]},
      { path: "/index", redirect: { name: "index"} },
      { path: "/", redirect: { name: "index"} },
      { path: "*",  redirect: { name: '404'}}
 
  ]
})

const perms = ['404', 'login', 'unauthorized', 'index', 'account']
const roles = []
router.beforeEach(async function (to, from, next) {
  await beforeEach({to, from, next, router, perms, roles})
})

export default router



