<template>
    <div class="contentBox custInput">
        <bread-crumb/>
        <div class="container whiteBg">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="批量录入" name="first">
                    <div class="batchInput">
                        <div class="batchInputBox">
 <el-input placeholder="请选择文件" :value="uploadFileName" :disabled="true"></el-input>
                 <!-- <el-button type="success" class="uploadClass">
                        <el-upload action="a" ref="uploader" 
                            class="avatar-uploader" 
                            :show-file-list="false" 
                            :file-list="fileList3"
                            :http-request="({file}) => this.file = file" >
                            选择文件
                        </el-upload>
                         </el-button> -->

                    <el-upload action="a" ref="uploader" 
                            class="avatar-uploader" 
                            :show-file-list="false" 
                            :http-request="({file}) => this.file = file" >
                           <el-button slot="trigger" type="success" class="chooesFile">选取文件</el-button>
                        </el-upload>
                             <a type="primary"  :href="downloadTemp"  target="_blank">
                                 <el-button type="primary" class="nextTemplate">下载模版</el-button>
                             </a>
                        </div>
                       
                    </div>
                     <div class="inputFoot" style="border-top: 1px solid #ecf0f1;">
                        <el-button type="primary" @click.stop="submitFile" :loading="submitFileLoading">提交</el-button>
                     </div>
                </el-tab-pane>
                <el-tab-pane label="单个录入" name="second">
                    <div class="singleInput">
                        <el-form :label-position="labelPosition" label-width="35%" :model="formLabelAlign" :rules="rules"  ref="formLabelAlign">
                            <el-form-item label="姓名："  prop="custName">
                                <el-input v-model="formLabelAlign.custName"></el-input>
                            </el-form-item>
                            <el-form-item label="身份证号："  prop="custCertNo">
                                <el-input v-model="formLabelAlign.custCertNo"></el-input>
                            </el-form-item>
                            <el-form-item label="手机号："  prop="custTel">
                                <el-input v-model="formLabelAlign.custTel"></el-input>
                            </el-form-item>
                            <el-form-item label="借款金额："  prop="loanAmt">
                                <el-input v-model="formLabelAlign.loanAmt"></el-input>
                            </el-form-item>
                              <el-form-item  style="border-top: 1px solid #ecf0f1;">
                                <div class="inputFoot">
                                     <el-button type="primary" @click.stop="submitnOne" :loading="submitLoading">提交</el-button>
                                     <el-button  type="danger" @click="resetForm('formLabelAlign')">重置</el-button>
                                </div>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-tab-pane>
            </el-tabs>
           
        </div>
    </div>
</template>
<script>
import Vue from "vue";

import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
@Component
@InjectComponents({ BreadCrumb })
export default class CustInput extends Vue {
    activeName = 'first'
    labelPosition = 'right'
    formLabelAlign= {custName: '',custCertNo: '',custTel: '',loanAmt: ''}
    submitLoading =false
    submitFileLoading =false
    file = null
 rules = {
    custName: [
      {trigger: "blur", required: true, message: '请输入姓名'},
      {whitespace: true, message: '不能包含空格'},
      {max:30 , message: '长度不能超过30个字符'},
    ],
    custCertNo: [
      {trigger: "blur", required: true, message: '请输入身份证'},
      {whitespace: true, message: '不能包含空格'},
      {trigger: "blur,change", validator: (rule, value, callback) => {
         if (this.$helper.tools.isIdCard(value)) return callback()
        return callback(new Error("无效身份证"))
      }}
    ],
    custTel: [
         {trigger: "blur", required: true, message: '请输入手机号码'},
        { required: true, message: '手机号码不能为空'},
        {trigger: "blur, change", validator: (rule, value, callback) => {
            if (value.length == 11 && /[0-9]{11}/.test(value)) return callback()
            return callback(new Error('请输入长度为11位有效的手机号码'))
         }}
    ],
    loanAmt: [
         {trigger: "blur", required: false, message: '请输入借款金额'},
        {trigger: 'blur, change', max: 9, message: '长度不得超过9个字符' },
         {trigger: "blur, change", validator: (rule, value, callback) => {
             if(!value) return callback()
             var isNum=/^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
            if (isNum.test(value)) return callback()
            return callback(new Error('请输入正确的金额'))
         }}
    ]
  }  
  
  handleClick(){
  }

  upload2(){
      alert(111)
  }
  
  async submitnOne(){
    if(!await this.$helper.tools.async(this.$refs['formLabelAlign'].validate)) return
        this.submitLoading =true
        const rsp = await this.$helper.fetch('/api/custMgr/addCustInfo').setParams({...this.formLabelAlign}).send()
        this.submitLoading =false
        if (!rsp.success) return rsp.success
        this.$notify.success({title: '成功',message: '提交成功',duration:4000});
        this.$refs['formLabelAlign'].resetFields();//添加成功后清空表单
  }

    resetForm(formName) {
        this.$refs[formName].resetFields();
    }

    async submitFile() {
    if(this.file ==null || this.file.name =="") 
        return this.$notify.error({title: '提示',message: '请选择文件!',duration:4000})
        const extension = this.file.name.substr(this.file.name.length-4) === 'xlsx'
        if (!extension) {return this.$notify.error({title: '提示',message: '上传模板只能是 xlsx格式!',duration:4000});}
        this.submitFileLoading =true
        const rsp = await this.$helper.fetch('/api/custMgr/inportCustInfo').setFile("custFile",this.file).send()
        this.submitFileLoading =false
        if (!rsp.success) return rsp.success
        this.$notify.success({title: '提示',message: "已成功上传"+rsp.data.successNum+'条',duration:4000});
    }

    @Computed downloadTemp(){
        return `/api/custMgr/downloadCustTemp`
    }

    @Computed uploadFileName() {
        return (this.file || {}).name || ''
    }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
/* .inputFoot{border-top: 1px solid #ecf0f1;height: 59px} */
.inputFoot{height: 59px}
.inputFoot .el-button--primary{float: right;width: 120px;margin-top: 23px; margin-left: 10px}

.inputFoot .el-button--danger{float: right;width: 120px;margin-top: 23px;background-color: #a9a6a6;}
/* .avatar-uploader{height: 100%;width: 100%;} */
.el-upload{line-height: 50px}
.avatar-uploader{display: inline-block;}
.chooesFile{ width: 100px;  height: 50px; line-height: 25px;}
.nextTemplate{display: inline-block; width: 98px;height: 50px;line-height: 25px;}
.batchInputBox{ margin: 0 auto; width: 620px;}
</style>


