<template>
  <div class="self-grid" v-loading="loading" element-loading-text="查询中...">
    <slot name="query" :form="value" :query="query" :reset="reset"/>
    <el-table ref="grid" :data="dataSource" highlight-current-row @selection-change="__selectionChange" @row-click="__rowClick" >
      <!-- <el-table-column type="selection" width="55" label="全部" class-name="selection-col" :class-name="selectionClass" v-if="multi || checkBox"/> -->
      <el-table-column v-if="multi || checkBox" :render-header="__selectionRenderHeader" width="70" class-name="self-grid__selection">
        <template slot-scope="{row, $index}">
          <label class="el-checkbox" @click.stop.prevent="() => toggleSelectIndex($index)"><span :class="`el-checkbox__input ${selectedRows.indexOf(row) != -1 ? 'is-checked' : ''}`"><span class="el-checkbox__inner"></span><input type="checkbox" class="el-checkbox__original" value=""></span> <span>&#12288;&#12288;</span></label>
        </template>
      </el-table-column>
      <slot>自定义列表</slot>
    </el-table>
    <div class="self-grid-page">
      <el-pagination v-if="isPage" layout="total,prev, pager, next, ->, jumper" :current-page="page.pageNo" :total="page.totalSize" :page-size="page.pageSize" @current-change="pageNo =>  this.load({pageNo})"/>
    </div>
  </div>
</template>
<script> 
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'

import GridSelectionCol from './GridSelectionCol'

@Component
@InjectComponents({c: GridSelectionCol})
export default class Grid extends Vue {
  
  @Prop({type: Object, required: false, default: () => ({}) }) value

  @Prop({type: Boolean, required: false, default: true}) autoload
  @Prop({type: Boolean, required: false, default: true}) multi
  @Prop({type: Boolean, required: false, default: true}) checkBox
  @Prop({type: String, required: false, default: null}) url
  @Prop({type: [Function, Object], required: false, default: () => ({})}) params
  @Prop({type: Boolean, required: false, default: true}) filterBlankParams
  @Prop({type: Boolean, required: false, default: true}) isPage
  @Prop({type: Number, required: false, default: 1}) pageNo
  @Prop({type: Number, required: false, default: 10}) pageSize

  loading = false

  defaultValue = {}
  page = { pageNo: 1, pageSize: 10, totalSize: 0 }
  dataSource = []

  selectedRows = []
  selectedIndexMap = {}

  @Computed data() {
    return {dataSource}
  }

  @Computed selectedAll() {
    return this.dataSource.length > 0 && this.selectedRows.length == this.dataSource.length
  }


  @Computed selectionClass() {
    return this.multi ? 'multi-checkbox' : 'single-checkbox'
  }

  __selectionRenderHeader(h, {column, _self, store}) {
    return h(GridSelectionCol, {
      attrs: { value: this.selectedAll, disabled: !this.multi },
      on: { input: this.toggleSelectedAll }
    })
  }

  __selectionClick(...args) {
    console.log('object', ...args);
  }

  __selectionChange(rows = []) {
    
    this.selectedRows = [...rows]
  }

  __rowClick(row) {
    const grid = this.$refs.grid
    if (!this.multi) {
      this.$refs.grid.clearSelection();
    } 
    this.$nextTick(() => {
      this.$emit('rowClick', row)
      this.$emit('rowclick', row)
      this.$refs.grid.toggleRowSelection(row);
    })
  }

  __selectable(row, index) {
    if (index == 0) return false
    return true
  }

  toggleSelectIndex(index) {
    const row = this.dataSource[index]
    if (!row) return false
    this.__rowClick(row)
    return true
  }

  toggleSelectedAll() {
    const {store} = this.$refs.grid
    if (this.selectedAll) return store.clearSelection()
    store.mutations.toggleAllSelection.call(store, store.states)
    return this.selectedAll
  }

  async query() {
    await this.load({pageNo: 1})
    return this
  }

  setDefaultParams(params = {}) {
    this.defaultValue = {...this.defaultValue, ...params}
    return this
  }

  async reset() {
    await this.$emit('input', {...this.defaultValue})
    // console.log('reset', {...this.defaultValue}, {...this.value})
    setTimeout(() => this.load({pageNo: 1}), 0)
    return this
  }

  async load(params0 = {}) {
    this.loading = true
    
    let params1 = {}, params2 = {}, params3 = {}

    // 内置参数
    if (typeof this.params == "function") params1 = this.params()
    else if (typeof this.params == 'object') params1 = this.params

    if (typeof params1 == 'boolean' && params1 === false) return
    else if (typeof params1 != 'object') params1 = {}

    // 表单参数
    params2 = {...this.value}
    
    // 分页参数
    if (this.isPage) params3 = {...this.page}

    let params = {...this.defaultValue, ...params1, ...params2, ...params3, ...params0}
    if (this.filterBlankParams) {
      let params9 = params, value
      params = {}
      for(let key of Object.keys(params9)) {
        value = params9[key]
        if (value == null) continue
        if (typeof value == 'undefined') continue
        if (typeof value == 'string') {
          value = value.trim()
          if (value.length == 0) continue
        }
        params[key] = value
      }
    }
    
    const rsp = await this.$helper.fetch(this.url).setParams(params).send()
    this.loading = false
    // console.log(rsp.success, rsp.message, 'grid')
    if (!rsp.success) return
    
    const {list: dataSource = [], pageNo = 1, pageSize = 10, totalSize = 0} = rsp.data
    this.dataSource = dataSource
    if (pageNo <= 0) this.page = {pageNo: 1, pageSize, totalSize}
    else this.page = {pageNo, pageSize, totalSize}
    // console.log("page", {...this.page}, rsp.data)
    // console.log('query', {...this.value}, {...this.defaultQuery});
  }

  @Lifecycle created() {
    this.defaultValue = {...this.value}
    this.page = {pageNo: this.pageNo, pageSize: this.pageSize}
    if (!this.autoload) return
    if (typeof this.url != "string" || this.url.trim().length == 0) return
    this.$nextTick(this.load)
  }
}
</script>

<style>
.self-grid {padding: 5px 0;}
.self-grid > .el-loading-mask { background-color: rgba(255, 255, 255, 0.1)}
.self-grid-page {width: 100%; display: flex; flex-direction: row-reverse; margin-top: 5px;}
th.single-checkbox > div > .el-checkbox { display: none!important; }
.el-table--enable-row-transition .el-table__header th.self-grid__selection .cell,
.el-table--enable-row-transition .el-table__body td.self-grid__selection .cell{  padding: 0 5px!important;}
.pageBtn{border: 1px solid #d1dbe5;width: 50px;height: 28px;margin-top: 2px;padding: 0;border-radius: 0;cursor: pointer}
.pageBtn span{color: #d1dbe5}
.pageBtn:hover{border-color: #55ccd6}
.pageBtn:hover span{color: #55ccd6}
</style>





