import {Module, State, Action, Getter, Mutation} from 'vue-decorators';

@Module({namespaced: true})
export default class AuthModule {
    @State currentUserProfile = null
    @State loginStatus = false
    @State fetched = false
    @State currentRouterName = 'index'
    @State routerKeys = {}

    @Getter userName(state) {
      return (state.currentUserProfile || {}).userName
    }

    @Getter userId(state) {
      return (state.currentUserProfile || {}).userId
    }


    @Mutation setCurrentRouterName(state, currentRouterName) {
      const {[currentRouterName]: count = 0} = state.routerKeys
      state.routerKeys[currentRouterName] = count > 9999 ? 1 : count + 1
      state.currentRouterName = currentRouterName
    }

    @Mutation setCurrentUserProfile(state, currentUserProfile = null){
      state.fetched = true
      state.loginStatus = currentUserProfile != null
      state.currentUserProfile = currentUserProfile
    }
}


