<template>
  <div class="roleManage contentBox">
  
          <div class="historyReportClass">
            <span>使用时间：{{busiDate}}</span>
            <span >姓名：{{custName}}</span>
              <div class="legendImg">
               <img src="../assets/custimg/icon_legend.png" />
            </div>
          </div>


    <div class="container whiteBg">
      <div class="tables">
    <self-grid ref="grid" url="/api/reportMgr/queryProdIndcInfo"  v-model="queryForm"  :multi ="false" :checkBox="false">
        <el-table-column prop="idntName" label="指标名称"/>
         <el-table-column prop="indcLvl" label="参数值">
            <template slot-scope ="{row}">
              <img v-if="row.indcLvl == '10022001'"  src="../assets/custimg/icon_hint_05.png" alt="" class="indImg"> 
              <img v-if="row.indcLvl == '10022002'" src="../assets/custimg/icon_hint_04.png" alt=""  class="indImg"> 
              <img v-if="row.indcLvl == '10022003'" src="../assets/custimg/icon_hint_03.png" alt=""  class="indImg"> 
              <img v-if="row.indcLvl == '10022004'" src="../assets/custimg/icon_hint_02.png" alt=""  class="indImg"> 
              <img v-if="row.indcLvl == '10022005'" src="../assets/custimg/icon_hint_01.png" alt=""  class="indImg"> 
          </template>
      </el-table-column>
      </self-grid>
      </div>
    </div>

  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";

//码表
require('@/store/cache').cacheCodes('billType', 'roleType','status')
@Component
@InjectComponents({ BreadCrumb})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
     @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
    @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
    @Prop({type: Boolean, required: false, default: true}) autoload
 

   queryForm= {}
   form ={ data:''}
   busiDate = null
   custName = null
  
      @Lifecycle created() {
        console.log("fjdk",this.params)
        this.form = {...this.form, ...this.params}
        const {billId = '',busiDate ='',custName=''} = this.form
        this.busiDate =busiDate
        this.custName = custName
        this.queryForm = {...this.queryForm,billId}
  //  this.$refs.grid.setDefaultParams({billId}).load()//内置一个值
    }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
.userTime{ border-top:  1px solid #ecf0f1;line-height: 42px;}
.custNameClass{    float: right; margin-right: 20px;}
.dangerLeve{float: right}
.cardHeader{background-color: #efefef;height: 30px;line-height: 30px;}
.cardFooter{margin-top: 15px}
.el-button{width: 80px;    line-height: 10px; border: 0px solid #fff;}
.el-card{border: 1px solid #c0c0c0;box-shadow: none}
span{color: #6b7074;}
.el-checkbox__label{font-size: 16px !important;}
.inputFoot{border-top: 1px solid #ecf0f1;height: 59px;margin-top: 30px;}
.inputFoot .el-button--primary{float: right;width: 120px;margin-top: 23px}
.button .el-button--primary{float: right;width: 120px;margin-top: 23px}

.el-col-offset-2{margin-left: 0}
.el-col-8{padding: 0 10px 20px 10px}
.elbutton .el-radio-button__orig-radio:checked+.el-radio-button__inner{background-color: #43c386;}
.elbutton span{border:0px solid #bfcbd9;}
.tables{margin-top: 12px}
.legendImg{float: right; margin-right: 20px;margin-top: -5px;}
.indImg{width:25px;}

</style>


