<template>
  <div class="full login">
    <div class="loginBox full">
      <img src="../assets/login/kanjiagou-.png" alt="" class="loginLogo" >
        <template  v-if="keyupEnter" >
        <el-form ref="form" class="loginForm" :model="form" :rules="rules" @keyup.enter.native="submit">
        <img src="../assets/login/baseboard.png" alt="" class="loginFormBg">
        <div class="loginFormBox">
          <h1>用户登录</h1>
          <el-form-item prop="loginNo">
             <img src="../assets/login/icon_yonghuming.png" class="loginImg">
            <el-input placeholder="请输入正确的手机号" :maxlength="20" v-model="form.loginNo" auto-complete="off" :autofocus="true"/>
            </el-form-item>
          <el-form-item prop="loginPwd">
            <img src="../assets/login/icon_mima.png" class="loginImg">
            <el-input type="password" placeholder="密码"  :maxlength="20" v-model="form.loginPwd" auto-complete="off"/>
            </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 100%;" :loading="submitLoading"  @click="submit">登录</el-button>
          </el-form-item>
        </div>
      </el-form> 
     </template>


      <template  v-if="!keyupEnter" >
          <el-form ref="form" class="loginForm" :model="form" :rules="rules" >
        <img src="../assets/login/baseboard.png" alt="" class="loginFormBg">
        <div class="loginFormBox">
          <h1>用户登录</h1>
          <el-form-item prop="loginNo">
             <img src="../assets/login/icon_yonghuming.png" class="loginImg">
            <el-input placeholder="请输入正确的手机号" :maxlength="20" v-model="form.loginNo" auto-complete="off" :autofocus="true"/>
            </el-form-item>
          <el-form-item prop="loginPwd">
            <img src="../assets/login/icon_mima.png" class="loginImg">
            <el-input type="password" placeholder="密码"  :maxlength="20" v-model="form.loginPwd" auto-complete="off"/>
            </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 100%;" :loading="submitLoading"  @click="submit">登录</el-button>
          </el-form-item>
        </div>
      </el-form> 
         </template>
     
    </div>
    <self-dialog ref="sendCodeForm" title="验证码" v-model="sendCodeFormVisible" @close="closesendCodeForm">
      <template slot-scope="{close, params}">
        <send-code-form :close="close" :params="params"/>
      </template>
    </self-dialog>
    <p class="CopyRightNo" style="position: absolute;bottom: 0;">CopyRight &copy;2016-2018 律军风控 版权所有 蜀ICP备16016755号-7</p>
  </div>
</template>

<script>
import Vue from 'vue'
import {Component, InjectComponents, Data, Prop, Watch, Lifecycle, Computed, Filter, State, Action, Mutation} from 'vue-decorators'
import sendCodeForm from './sendCodeForm'

@Component
@InjectComponents({sendCodeForm})
export default class Network extends Vue {

  @State("auth") auth
  @Mutation("auth/setCurrentUserProfile") setCurrentUserProfile
  @Action('cache/run') cacheRun
  keyupEnter =true
  sendCodeFormVisible = false
  random = new String(Math.random()).substr(2)
  submitLoading = false;
  form = {loginNo: '',loginPwd: '',msgCode:''}
  rules = {
    loginNo: [
      {trigger: "blur", required: true, message: '请输入手机号'},
      {whitespace: true, message: '不能包含空格'},
       {trigger: "blur, change", validator: (rule, value, callback) => {
            if (value.length == 11 && /[0-9]{11}/.test(value)) return callback()
            return callback(new Error('请输入长度为11位有效的手机号码'))
      }}
    ],
    loginPwd: [
      {trigger: "blur", required: true, message: '请输入密码'},
      {whitespace: true, message: '不能包含空格'},
      {max: 36, message: '长度不能大于36个字符'}
    ]
  }
  closesendCodeForm(){
    this.keyupEnter =true
  }

  async submit() { 
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return

    this.submitLoading = true
    const rsp = await this.$helper.fetch('/api/login').setParams({...this.form}).send()
    this.submitLoading = false
    if (!rsp.success) return rsp.success
    const {loginStatus = false, firstLogin = false,currentUserProfile} = rsp.data
    if(firstLogin){
       this.keyupEnter =false
        this.sendCodeFormVisible = true;
        this.$refs.sendCodeForm.setParams(this.form)
        return
    }
    if (!loginStatus) return this.$notify({title: '提示',message:  '登录失败',type: 'error',duration:4000});

    // 执行缓存
    await this.cacheRun()

    this.$notify({title: '成功',message:  '登录成功',type: 'success',duration:4000});

    this.setCurrentUserProfile(currentUserProfile)
    const {currentRouterName = 'index'} = this.auth
    this.$router.replace({name: currentRouterName})
  }
}
</script>

<style scoped>
body{margin: 0}
.login {background: url('../assets/login/background.png') center center no-repeat; height: 100%; background-size: 100% 100%;}
.login .loginBg{width: 100%; }
.login .loginBox{position: relative;}
.login .loginForm{position: absolute;top: 50%; left: 50%; margin-left: -260px; margin-top: -200px;width: 520px;}
.login .loginFormBox{position: absolute;top: 0;width: 516px;text-align: center;left: 3px}
.login .loginFormBox .el-form-item{    width: 300px !important;margin-bottom: 22px;margin-left: 110px;}
.login .loginLogo{position: absolute;top: 5%;width: 177px;left: 50%;margin-left: -82px}
.login .loginFormBg{width: 520px;}
/* .loginBox .loginFormBg { width: 360px; padding: 50px 70px; background-image: url('../assets/login/baseboard.png'); border-radius: 16px 16px; position: absolute; } */
.loginBox .loginForm h1 { line-height: 85px; font-size: 32px; color: #2ecc71;margin: 0;margin-bottom: 42px;border-bottom: 1px solid rgba(46, 204, 113, 0.6)}
.loginBox .el-form-item__content input{line-height: 42px}
.el-input, .el-input__inner{height: 42px;}
.loginBox .el-form-item__content button{line-height: 42px}
.login .el-button--primary{background-color: #2ecc71;border: none;padding: 0;margin-top: 50px; }

/* .loginBox .loginCaptcha { width: 100%; height:100%; border-radius: 4px 4px; cursor: pointer;} */
.loginImg{position: absolute;z-index: 2;top: 8px;left: 10px; width: 16px;}
</style>


