<template>
    <div class="dangerCount contentBox">
        <bread-crumb/>
        <div class="container whiteBg">
           <el-form :inline="true" class="demo-form-inline dateTimeInline" :model="form" ref="form">
               <el-form-item prop="beginTime" label="时间：">
                    <el-date-picker :editable="false" type="date" placeholder="开始日期"
                     v-model="form.beginTime_" @change="change" style="width: 100%;"></el-date-picker>
                  </el-form-item>

                <el-form-item >
                    <div>-</div>
                  </el-form-item>
                <el-form-item prop="loginId">
                    <el-date-picker   :editable="false"  
                    @change="value => form.endTime = value" 
                    type="date" placeholder="结束日期" 
                    v-model="form.endTime_" style="width: 100%;"></el-date-picker>
                </el-form-item>
                <el-form-item class="buttonTeam">
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </el-form>
            <div class="chartBox" :style="`height:${innerHeight - 250}px;`">
                <div id="riskTrendsDanger" style="width: 100%;height: 100%;" v-loading="loading" element-loading-text="查询中...">
                    <div v-show="visable" style="width: 100%;height: 100%;" ref="riskTrendsDanger"/>
                    <div class="nohaveData" v-show="!visable">
                        <img src="../assets/custimg/nodata.png"/>
                       <p>暂无数据</p>
                        </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Vue from "vue";

import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter, State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
import echarts from 'echarts'
@Component
@InjectComponents({ BreadCrumb })
export default class homepage extends Vue {

    @State(state => state.layout.innerHeight) innerHeight
    loading = false
    visable = false
    chart = null
    form = {beginTime:'',endTime:''}
    change(value) {
        this.form.beginTime = value
    }
    async onSubmit(){
        const {beginTime = '',endTime =''} = this.form
        var startTmp=beginTime.split("-");  
        var endTmp=endTime.split("-");  
        var sd=new Date(startTmp[0],startTmp[1],startTmp[2]);  
        var ed=new Date(endTmp[0],endTmp[1],endTmp[2]);  
        if(sd.getTime()>ed.getTime()){  
        this.$alert('开始日期不能大于结束日期', '提示',{
            confirmButtonText: '确定',
            
        })
       
           return false;     
         }     
                    
        this.loadData()
    }

    async loadData(){
        this.loading = true
        const rsp = await this.$helper.fetch('/api/reportMgr/queryRiskStatistics').setParams({...this.form}).send()
        this.loading = false
        if (!rsp.success) return rsp.success
        this.visable = rsp.data.list.length != 0
        if (!this.visable) return
        const {riskTrendsDanger = {}} = this.$refs
        this.$nextTick(() => {
            this.riskTrendsedAccChart(riskTrendsDanger,{ title :"", optionsData : rsp } )
        })

    }

    dispatchClick(...args) {
    }

    @Lifecycle async created() {
        this.loadData()
    }


    riskTrendsedAccChart(el, { title = "", optionsData = [], options = [], otherOpts = {} } = {}) {
     const timeArray =[]//时间
     const numArray =[]//num
     const selfNum =[] 
     
     const datas = optionsData.data.list
     for(var i in datas){
        let abc = this.$helper.tools.formatDate(datas[i].instTime, "YYYY-MM-DD").substring(5).replace('-','.');
        if(abc.substring(0,1) == '0') timeArray.push(abc.substring(1))
        else  timeArray.push(abc)
        numArray.push(datas[i].rstNum)
        selfNum.push(this.$helper.tools.formatDate(datas[i].instTime, "YYYY-MM-DD"))

     }
    const option = {
        color: ['#43c386'],
        title: {
            text: '人数',
            textStyle:{ color:'#c8c8c8',fontSize:'14px',}
        },
        tooltip: {trigger: 'axis'},
        //legend: {data:['风险统计']},
        grid: {left: '3%', right: '4%',bottom: '3%',containLabel: true},
        xAxis: {
            type: 'category',
            boundaryGap: false,
            axisLine: {
                        lineStyle: {type: 'solid',color: '#cccccc',width:'1'}//color左边线的颜色width:'1'//坐标线的宽度
                    },
            data: timeArray
           
        },
        yAxis: {
            type: 'value',
            axisLine: {
                    lineStyle: {type: 'solid',color:'#cccccc',width:'1'}
                },
        },
        series: [
            {name:'风险统计', type:'line',
            stack: '总量',
            symbolSize:'10',//圆圈大写
            itemStyle:{
                normal:{
                    color: '#2ecc71',
                    lineStyle:{ width:'4',}//线条粗细
                }
            
            },
            data:numArray,
            rawdate: selfNum
        },
        
        ],
          
    };

    
        


 echarts.init(el).setOption(option)
   window.addEventListener("resize",function () {//添加一个单击事件（单击时触发function函数）  
      echarts.init(el).resize();
    })
    echarts.init(el).on('click', (param) => {
        var cloudid, num; 
        cloudid =option.series[param.seriesIndex]
        num = param.dataIndex
        cloudid = cloudid.rawdate[num]
        var params ={name:cloudid}
        this.$emit('open',params)
  });


   
}






}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
.nohaveData{text-align: center;margin-top: 20px}
.nohaveData p {color: #d9ddda;margin: 0;}

</style>


