import {Message, MessageBox} from 'element-ui'
import fetch from 'isomorphic-fetch'
import tools from './tools'
import Crypt from './crypt'

let emptyFn = () => {};

class FetchService {
  
  constructor(url, {headers = {}, params = {}, options = {}} = {}) {
    this.url = url;
    this.headers = {...this.headers, ...headers};
    this.params = params;
    this.options = options;
  }

  supportCmd = true

  autoTirm = true

  filterEmpty = false

  filterBlank = false

 
  enableCrypto = process.env.NODE_ENV === 'production'
  cryptoRule

 
  url

  dataType = 'json'
  
  headers = {
    "Accept": "application/json; charset=utf-8"
  }
  params = {}
  options = {}
  files = {}

  success
  status
  message
  method = 'POST'

  errorHandlers = []

  setMethod(method) {
    this.method = method.toUpperCase();
    return this;
  }

  get() {
    this.method = 'GET';
    return this;
  }

  post() {
    this.method = 'POST';
    return this;
  }

  setParams(params = {}) {
    this.params = {...this.params, ...params};
    return this;
  }

  setParam(key, value) {
    this.params[key] = value;
    return this;
  }

  appendParam(key, value) {
    if (tools.isNotUndefinedOrNull(value)) return this.setParam(key, value);
    if (tools.isArray(this.params[key])) this.params[key].push(value);
    this.params[key] = [this.params[key], value];
    return this;
  }

  setFile(key, file) {
    if (tools.isNotObject(file)) return this;
    this.files[key] = file;
    return this;
  }

  onError(errorHandler = emptyFn) {
    this.errorHandlers.push(errorHandler);
    return this;
  }

  openCrypto() {
    this.enableCrypto = true;
    return this;
  }
  closeCrypto() {
    this.enableCrypto = false;
    return this;
  }
  setEnableCrypto(enableCrypto = true) {
    this.enableCrypto = enableCrypto;
    return this;
  }
  setCryptoRule(cryptoRule) {
    this.cryptoRule = cryptoRule;
    return this;
  }
 
  beforeSend = emptyFn
  
  complete = emptyFn

  error = emptyFn

  get() { return this.method = 'GET', this.send() }
  post() { return this.method = 'POST', this.send() }
  reset() { return this.sendCount = 0, this }

  get enableReSend() {
    return this.sendCount <= 2
  }
  
  // 请求次数
  sendCount = 0

  send = () => {
    this.sendCount += 1
    if (!this.enableReSend) throw new Error("不能发送两次请求")
    return new Promise(async (resolve, reject) => {
      let conentType = this.headers['content-type'];
      const isJsonData = typeof conentType == 'string' && conentType.toLocaleLowerCase().indexOf('application/json') != -1

      let body;
      if (isJsonData) {
        body = inJsonData;
      
      } else {
        body = new FormData();

        //检查参数有效性
        if (['json'].indexOf(this.dataType) == -1){
          return resolve({
            success: false,
            message: `数据类型【dataType:${this.dataType}】目前还未支持。`
          });
        }

        if (!this.supportCmd) {
          //不支持Cmd
          for(let key in this.params) {
            body.set(key, this.params[key])
          }
        } else {
          let p = {}, v;
          //支持Cmd
          for(let key in this.params) {
            v = this.params[key];
            if (v == null) continue;
            if (typeof v == 'object') { p[key] = JSON.stringify(v); continue}
            if (typeof v == 'number') { p[key] = v; continue }
            if (typeof v != 'string') continue;

            if (this.autoTirm || this.filterBlank) v = v.trim();
            if (this.filterBlank && v === '') continue;
            if (this.filterEmpty && v === '') continue;

            p[key] = v; 
          }
          
          for(let name in this.files) {
            body.append(name, this.files[name]);
          }

          let inStr = JSON.stringify(p);
          if (this.enableCrypto && Crypt.enable()) {
            // console.log('fetch', {
            //   '请求接口：': this.url,
            //   '加密前参数：': inStr
            // });
            //加密前
            let {rule, cryptRule, cryptData} = Crypt.encrypt(inStr);
            // console.log("发送前：", rule, cryptRule, cryptData)
            this.cryptoRule = rule;
            body.append('cryptEnable', true);
            body.append('crypt', cryptRule);
            body.append('in', cryptData);
          } else {
            body.append('in', inStr);
          }
          
        }
      }
      let headers = this.headers;

      const resovleHandler = async (rsp) => {
        let {message = '操作成功', out: data = {}, success = true, status = 200} = rsp;
        // console.log("发送后：", {...this}, data)
        if (this.enableCrypto && tools.isNotObject(data)) {
          let dataCrypt = Crypt.decrypt(data, this.cryptoRule);
          // console.log('解密规则：', this.cryptoRule);
          // console.log('解密前：', data);
          // console.log('解密后：', dataCrypt);
          data = JSON.parse(dataCrypt);
        }

        if (success && 200 != status) success = false;
        if (404 == status) message = "请求不存在"

        // status = 500;
        // success = false;
        // message = '服务升级中...';
        rsp = {status, success, message, data}
        if ((status != 200 || !rsp.success)) {
          let isBreakError = false, r;

          for (let error, index = 0; index < this.errorHandlers.length; index++) {
            error = await this.errorHandlers[index];
            r = await error(rsp, {fetch: this, resolve, reject})
            if (typeof r == 'boolean' && r === false) {
              isBreakError = true;
              break;
            }
          }

          if (!isBreakError) {
            // console.log(errorGlobalHandlers.length);
            for (let error, index = 0; index < errorGlobalHandlers.length; index++) {
              error = errorGlobalHandlers[index];
              r = await error(rsp, {fetch: this, resolve, reject});
              if (typeof r == 'boolean' && r === false) {
                isBreakError = true;
                break;
              }
            }
          }
          if (!isBreakError) {
            let model = Message({message, type: 'error'});
          }
        }
        
        resolve(rsp);
      }

      
     try {
        fetch(this.url, {...this.options, headers, body,method: this.method, credentials: 'include'}).then(response => {
          const status = response.status;
          const success = status >= 200 && status < 300;
          if (status == 504) return resovleHandler ({status, success: false, message: '网络错误'})
          if (this.dataType == 'json') {
            try {  
              response.json().then(data => {
                resovleHandler({...data, status});
              });
            } catch(e) {
              resovleHandler ({status, success: false, message: '网络错误'});
            }
          }
        })
        //.catch(_ => resovleHandler({status: 404, success: false, message: '网络错误'}));
      } catch(e) {
        // resovleHandler({status: 404, success: false, message: '网络错误'});
      }
      
    });
  }
}

let errorGlobalHandlers = [];
export function onError(errorHandler = emptyFn) {
  errorGlobalHandlers.push(errorHandler);
}
export function onErrorBefore(errorHandler = emptyFn) {
  errorGlobalHandlers.unshift(errorHandler);
}

const fetchService = function(url, ...args) {
  return new FetchService(url, ...args)
};

fetchService.onError = onError;
fetchService.onErrorBefore = onErrorBefore;

export const registerCrypt = (exponent, modulus) => Crypt.register(exponent, modulus);
fetchService.registerCrypt = registerCrypt;

export default fetchService;


