<template>
  <el-menu-item v-if="!isSubMenu" :index="menu.index" :class="activeClass" ref="menu" :id="activeId">
    <i v-if="hasIcon" :class="`iconfont ${menu.icon}`"></i>
    <template v-if="level == 1">&nbsp;&nbsp;&nbsp;&nbsp;</template>
    {{menu.text}}
  </el-menu-item>
  <el-submenu v-else :index="menu.index">
    <template slot="title">
      <i v-if="hasIcon" :class="`iconfont ${menu.icon}`"></i>
      <template v-if="level == 1">&nbsp;&nbsp;&nbsp;&nbsp;</template>
      {{menu.text}}
    </template>
    <!-- <self-left-menu v-for="(_menu, index) in menu.children" :menu="_menu" :level="level + 1" :key="index" :active="active"/> -->
    <slot :menus="menu.children" :level="level + 1" :active="active"/>
  </el-submenu>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'

@Component
export default class SelfLeftMenu extends Vue {

  @Prop({type: Object, required: true, default: () => ({})}) menu
  @Prop({type: Number, required: true, default: 1}) level
  @Prop({type: String, required: false, default: 'index'}) active

  @Computed isSubMenu() {
    return typeof this.menu.children == 'object' && this.menu.children instanceof Array
  }

  @Computed hasIcon() {
    return typeof this.menu.icon == 'string' && this.menu.icon.length != 0
  }

  @Computed activeClass() {
    const {index = null} = this.menu
    return this.active == index ? 'is-active' : ''
  }

  @Computed activeId() {
    const {index = null} = this.menu
    return this.active == index ? 'defualtSelfLeftNavActiveIndex' : ''
  }
}
</script>




