<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="账号:" prop="name">
      <div>{{userName}}</div>
    </el-form-item>
    
    <el-form-item label="原密码:" prop="oldPwd">
        <el-input type="password" v-model="form.oldPwd" auto-complete="off"></el-input>
    </el-form-item>
      <el-form-item label="新密码:" prop="nPwd">
        <el-input type="password" v-model="form.nPwd" auto-complete="off"></el-input>
    </el-form-item>
      <el-form-item label="重复密码:" prop="retPwd">
        <el-input type="password" v-model="form.retPwd" auto-complete="off"></el-input>
    </el-form-item>

    <el-row>
      <el-col :span="12" style="text-align: right;"><el-button @click="close">取 消</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success" @click="submit" style="float:left;color: #fdf7f7" :loading="submitLoading">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter,Mutation} from 'vue-decorators'

@Component
export default class ModifyLoginPwdForm extends Vue{

  @Getter('auth/userName') userName
  @Getter('auth/userId') userId
   @Mutation("auth/setCurrentUserProfile") setCurrentUserProfile

  @Prop({type: Function, required: true, default: null}) close
  submitLoading =false

   


  form = {oldPwd: '',nPwd:'',retPwd:''}
  rules = {
      oldPwd: [
      {trigger: 'blur, change',  required: true, message: "请输入原密码"},
      
  
    ],
       nPwd: [
      {trigger: 'blur, change',  required: true, message: "请输入新密码"},
       {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/[A-Za-z].*[0-9]|[0-9].*[A-Za-z]/
          if (regex.test(value)) return callback()
          return callback(new Error('密码必须是数字和字母的组合'))
      }},
       {min: 6, message: '长度不能小于6个字符'},
     
    ],
       retPwd: [
       {trigger: 'blur, change',  required: true, message: "请输入重复密码"},
       {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/[A-Za-z].*[0-9]|[0-9].*[A-Za-z]/
          if (regex.test(value)) return callback()
          return callback(new Error('密码必须是数字和字母的组合'))
      }},
        {min: 6, message: '长度不能小于6个字符'}
     
    ]
  }
    
  async submit() {
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    const userId = this.userId
    const datas ={...this.form,userId}
    this.submitLoading = true
    const rsp = await this.$helper.fetch('/api/login/alterPwd').setParams({...datas}).send()
     this.submitLoading =false
    if (!rsp.success) return rsp.success
    this.$notify({title: '提示',message:  rsp.message,type: 'success',duration:4000});
    const rspLogin = await this.$helper.fetch("/api/login/logout").send()
    if (!rspLogin.success) return
    await this.setCurrentUserProfile(null)
    this.$router.replace({name: "login"})

    this.close(Math.random())
  }
}
</script>




