import moment from 'moment';

function DateUtil() {
		this.default_formatStr = "YYYY-MM-DD hh:mm:ss";//默认格式化字符串
	}
	
//内部函数 解析Date类型
DateUtil.prototype.__parseDate__ = function( date ) {
  return moment( date ).locale("zh_cn");
};

//内部函数 解析时间戳类型
DateUtil.prototype.__parseTime__ = function( time ) {
  var t, d, a;
  a = arguments;
  t = a[ 0 ];
  if ( typeof t != "number" ) {
    try{ t = Number( t ); } catch( e ) { return; }
  }
  var d = new Date( t );
  if ( typeof d != "object" && !d instanceof Date ) return;
  if ( d.getFullYear() == "1970" ) return ;
  return moment( d );
};

//内部函数 解析时间字符串
DateUtil.prototype.__parseStr__ = function ( str, parseFormatStr ) {
  return moment( str, parseFormatStr ).locale("zh_cn");
};

/**
 * @param dateOrTimeOrStr { date | time | dateStr } Date对象 时间戳 时间字符串
 * @param parseFormatStr 时间字符解析格式 dateOrTimeOrStr为时间字符串赋值 如其它可以省略
 * 实例
 *  tools.dateUtil.parse( new Date() );
 *  tools.dateUtil.parse( "2015/06/03", "YYYY/MM/DD" );
 *  tools.dateUtil.parse( "1461215526581" )
 *  tools.dateUtil.parse( 1461215526581 )
 */
DateUtil.prototype.parse = function( dateOrTimeOrStr, parseFormatStr ) {
  if ( arguments.length == 0 ) {
    return;
  }

  if ( dateOrTimeOrStr == null ) return;

  var d, p, m, a;
  
  a = arguments;
  
  if ( a.length == 1 ) {
    d = a[ 0 ];
    p = false;
  } else if ( a.length == 2 ) {
    d = a[ 0 ];
    p = a[ 1 ];
  } 
  
  if ( d == "object" && d instanceof Date ) m = this.__parseDate__( d );
  else if ( d == "string" ) 
    if ( d.trim ) d = d.trim();
    if ( p === false ) m = this.__parseTime__( d );
    else m = this.__parseStr__( d, p );
  
  if ( typeof m == "undefined" ) return d;
  
  return m;
};

/**
 * @param dateOrTimeOrStr { date | time | dateStr } Date对象 时间戳 时间字符串
 * @param parseFormatStr 时间字符解析格式 dateOrTimeOrStr为时间字符串赋值 如其它可以省略
 * @param formatStr 格式化字符串 默认YYYY-MM-DD hh:mm:ss 可省略
 * 实例
 *  tools.dateUtil.format( new Date() );
 *  tools.dateUtil.format( new Date(), "YYYY-MM-DD" );
 *  tools.dateUtil.format( "2015/06/03", "YYYY/MM/DD" );
 *  tools.dateUtil.format( "2015/06/03", "YYYY/MM/DD", "YYYY-MM-DD" );
 *  tools.dateUtil.format( "20150603", "YYYYMMDD", "YYYY-MM-DD" );
 *  tools.dateUtil.format( "20150603", "YYYYMMDD", "dddd" ); //星期几
 *  tools.dateUtil.format( "1461215526581" )
 *  tools.dateUtil.format( "1461215526581", "YYYY-MM-DD" )
 *  tools.dateUtil.format( 1461215526581 )
 *  tools.dateUtil.format( 1461215526581, "YYYY-MM-DD" )
 */
DateUtil.prototype.format = function( dateOrTimeOrStr, parseFormatStr, formatStr ) { 
  if ( arguments.length == 0 ) {
    return;
  }

  if ( dateOrTimeOrStr == null ) return;

  var d, p, f, m, t, a, r; 
  
  a = arguments;
  
  if ( a.length == 1 ) {
    d = a[ 0 ];
    p = false;
    f = this.default_formatStr;
  } else if ( a.length == 2 ) {
    d = a[ 0 ];
    p = false;
    f = a[ 1 ];
    if ( typeof f != "string" && f.length == 0 ) {
      f = this.default_formatStr;
    }
  } else if ( a.length == 3 ) {
    d = a[ 0 ];
    p = a[ 1 ];
    f = a[ 2 ];
    if ( typeof f != "string" && f.length == 0 ) {
      f = this.default_formatStr;
    }
  }
  
  t = typeof d;
  
  if ( t == "number" ) {
    d = d.toString();
    t = "string";
  }
  
  if ( t == "object" && d instanceof Date ) m = this.__parseDate__( d );
  else if ( t == "string" ) 
    if ( t.trim ) t = t.trim();
    if ( p === false ) m = this.__parseTime__( d );
    else m = this.__parseStr__( d, p );
  
  if ( typeof m == "undefined" ) return d;
  
  r = m.format( f );
  
  if ( r == "Invalid date" ) return d;
  
  return r;
};


DateUtil.prototype.getWeekInYear = function( dateOrTimeOrStr, parseFormatStr ) {
  var dateMoment, day, days, weeks;
  weeks = 0;
  dateMoment = this.parse( dateOrTimeOrStr, parseFormatStr );
  day = dateMoment.day(); //本周星期几
  days = dateMoment.dayOfYear(); //总天数
  
  if ( day == 0 ) day = 7;
  if ( day > 1 ) {
    weeks += 1;
    days = days - ( 7 - ( day - 1 ) );
  }
  weeks = weeks + Math.floor( days / 7 ) + ( days % 7 == 0 ? 0 : 1 );
  return weeks;
};

/**
 * @param dateOrTimeOrStr { date | time | dateStr } Date对象 时间戳 时间字符串
 * @param parseFormatStr 时间字符解析格式 dateOrTimeOrStr为时间字符串赋值 如其它可以省略
 * 实例
 *  tools.dateUtil.parse( new Date() );
 *  tools.dateUtil.parse( "2015/06/03", "YYYY/MM/DD" );
 *  tools.dateUtil.parse( "1461215526581" )
 *  tools.dateUtil.parse( 1461215526581 )
 */
DateUtil.prototype.parse2Date = function( dateOrStr, parseFormatStr ) {
  if (typeof dateOrStr == 'object' && dateOrStr != null) {
    if (dateOrStr instanceof Date) return dateOrStr;
  }
  if (typeof dateOrStr == 'string' || typeof dateOrStr == 'number') {
    var mdate = this.parse(dateOrStr, parseFormatStr);
    if (typeof mdate == 'object' && mdate != null && mdate instanceof moment) {
      return mdate.toDate();
    }
  }
  return null;
};

DateUtil.prototype.parse2Time = function( dateOrStr, parseFormatStr ) {
  if (typeof dateOrStr == 'number') return dateOrStr;
  if (typeof dateOrStr == 'object' && dateOrStr != null) {
    if (dateOrStr instanceof Date) return dateOrStr.getTime();
  }
  if (typeof dateOrStr == 'string') {
    var date = this.parse2Date(dateOrStr, parseFormatStr);
    if (typeof date == 'object' && date != null && date instanceof Date) {
      return date.getTime();
    }
  }
  return null;
};
	
export default new DateUtil();


