<template>
  <div class="myBill contentBox">
    <bread-crumb/>
    <div class="container whiteBg">
      <div>
        <el-row style="margin-left:-12px">
          <el-col :span="6" class="headerBox" :sm="8"  :xs="12">
              <img src="../assets/custimg/img_yu’e.png"/>
              <div class="headerText">
                  <p class="headerNo">余额:</p>
                  <p class="headerName">￥{{amtInfo.balAmt}}</p>
              </div>
          </el-col>
            <el-col :span="6" class="headerBox" :sm="8" :xs="12">
              <img src="../assets/custimg/img_benyuexiaofei.png"/>
              <div class="headerText">
                  <p class="headerNo">本月消费:</p>
                  <p class="headerName">￥{{amtInfo.costAmt}}</p>
              </div>
          </el-col>
      </el-row>
      </div>
      <self-grid ref="grid" url="/api/billMgr/queryOrgBillList" v-model="queryForm" :multi ="false" :checkBox="false"  >
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline dateTimeInline" :inline="true" :model="form">
               <el-form-item prop="beginTime" label="时间：">
                    <el-date-picker :editable="false" type="date" 
                    placeholder="开始日期" v-model="form.beginTime_"  @change="value => form.beginTime = value" style="width: 100%;"></el-date-picker>
                  </el-form-item>
                <el-form-item >
                    <div>-</div>
                  </el-form-item>
                <el-form-item prop="endTime">
                    <el-date-picker :editable="false" type="date"
                     placeholder="结束日期" v-model="form.endTime_"  @change="value => form.endTime = value" style="width: 100%;"></el-date-picker>
                </el-form-item>
                <el-form-item class="myBills">
                      <span>今天&nbsp;|&nbsp;最近：</span>
                    <el-radio-group v-model="form.dateType"  class="elRadioClass">
                      <!-- <el-radio-button label="今天" border><span>今天&nbsp;|&nbsp;最近：</span></el-radio-button> -->
                        <el-radio-button label="Month" border>30天</el-radio-button>
                        <el-radio-button label="Season">90天</el-radio-button>
                        <el-radio-button label="Year">365天</el-radio-button>
                    </el-radio-group>
                </el-form-item>
           <el-form-item class="buttonTeam">
            <el-button type="primary" @click="query">查询</el-button>
            <!-- <el-button type="danger resetEditClass" @click="reset">重置</el-button> -->
             </el-form-item>
          </el-form>
        </template>
        <el-table-column prop="insertTime" label="时间"> 
          <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.insertTime, "YYYY-MM-DD")}}</template>
        </el-table-column>
        <el-table-column prop="billType" label="消费类型"> 
            <template slot-scope="{row}">{{codeStorage.get('billType', row.billType)}}</template>
        </el-table-column>
        <el-table-column prop="userName" label="管理人员"> </el-table-column>
      <el-table-column prop="custName" label="客户姓名">
          <!-- <template slot-scope ="{row}">
             {{ row.custName.trim().substring(0,1)+"**"}}
          </template> -->
        </el-table-column>      
          <el-table-column prop="billAmt" label="使用费用"> </el-table-column>
        <el-table-column prop="prodName" label="使用风控产品"> </el-table-column>

      </self-grid>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";

import addUserForm from './addUserForm'
import editUserForm from './editUserForm'
import resetUserPwdForm from './resetUserPwdForm'

//码表
require('@/store/cache').cacheCodes('billType', 'roleType','status')
@Component
@InjectComponents({ BreadCrumb,addUserForm,editUserForm,resetUserPwdForm})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
   
    queryForm = {}
    amtInfo = {}
    form = { dateType:''}
   @Lifecycle async created() {
     const rsp = await this.$helper.fetch('/api/billMgr/queryOrgBillAmt').send()
      if (!rsp.success) return rsp.success
      this.amtInfo = rsp.data.amtInfo;

  }


}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
.headerBox{position: relative}
.headerBox img{width: 100%}
</style>


