<template>
<div class="chartBox" style="height: 190px;">
   <div id="emergencyNotice" style="width: 50%;height: 100%;" ref="emergencyNotice" v-loading="loading" element-loading-text="加载中...">
          <div v-show="visable" style="width: 100%;height: 100%;" ref="emergencyNotice"/>
           <div class="nohaveData"  v-show="!visable">
                    <img src="../assets/custimg/nodata.png"/>
                       <p>暂无数据</p>
              </div>
   </div>
    <div id="emergencyWarning" style="width: 50%;height: 100%;" ref="emergencyWarning" v-loading="loading" element-loading-text="加载中...">
       <div v-show="visable" style="width: 100%;height: 100%;" ref="emergencyWarning"/>
        <div class="nohaveData"  v-show="!visable">
                    <img src="../assets/custimg/nodata.png"/>
                       <p>暂无数据</p>
              </div>
    </div>
</div>
        
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'
import echarts from 'echarts'
@Component
export default class HomePage extends Vue{
   visable =false
   loading =false
  async loadData(){
    this.loading = true
    const rsp = await this.$helper.fetch('api/index/getRiskGeneralData').setParams({...this.form}).send()
    this.loading = false
    if (!rsp.success) return rsp.success

     const {riskInfo = {} }  = rsp.data
     // const riskInfo = {} 
    if(Object.keys(riskInfo).length != 0) this.visable = true
    if (!this.visable) return
    const {emergencyNotice = {}} = this.$refs
    const {emergencyWarning = {}} = this.$refs
       this.$nextTick(() => {
        this.emergencyNoticeChart(emergencyNotice,{ title :"", optionsData : rsp,color:['#f3b760', '#d9ddda'],name:'风险提醒人占总\n管理人比例' ,sigen:'warnCnt'} )
        })
        
       this.$nextTick(() => {
        this.emergencyNoticeChart(emergencyWarning,{ title :"", optionsData : rsp,color:['#e74c3c', '#d9ddda'],name:'风险预警人数占总\n管理人比例' ,sigen:'riskCnt'} )
       })

   
  }

  @Lifecycle mounted(){
    this.loadData()
  }

  emergencyNoticeChart(el, { title = "寝室学习氛围情况调查", optionsData = [], options = [] ,color = [],name ='',sigen=''} = {}) {

    const {riskInfo = {} }  = optionsData.data
    let riskCntAndwarnCnt = null
    let titleName = null
    let myChart=''
    if(sigen == "warnCnt"){
      titleName = '风险提醒人数'
      riskCntAndwarnCnt = riskInfo.warnCnt//提醒
            myChart = echarts.init(document.getElementById("emergencyNotice"));

    }
    if(sigen == "riskCnt"){
         titleName = '风险预警人数'
        riskCntAndwarnCnt = riskInfo.riskCnt //有风险
          myChart = echarts.init(document.getElementById("emergencyWarning"));
    }
    if(!riskCntAndwarnCnt) riskCntAndwarnCnt = 0
   

  let mngCnt = riskInfo.mngCnt //管理人数
  if(!mngCnt) mngCnt = 0
  const value = riskCntAndwarnCnt/mngCnt

  const option = {
     title: {
      x:'center', y:'bottom',text: name,
      textStyle:{fontSize :'12',color:'#6b7074',fontWeight:'normal' }
     },
    backgroundColor: '#fff',
    tooltip: {trigger: 'item',formatter: "{a} <br/>{b}: {c} "},
    //legend: {x:'center',y:'bottom',data:[name]},
    color: color,
    series: [
      {
        name: '风险概览',
        center:['50%','40%'],
        type: 'pie',
        radius: ['50%', '70%'],
        avoidLabelOverlap: false,
        data: [
          {
            value: riskCntAndwarnCnt,
              name: titleName,
            label: {
              normal: {show: true, position: 'center',formatter:function(){
                  return (value*100).toFixed(2)+'%'
              } }
            }
          },
          {
            value: (mngCnt-riskCntAndwarnCnt),
            name: '未提醒人数',
            label: {
              normal: {show: false,position: 'center'}
            }
          }
        ]
      }
    ]
  }
  myChart.setOption(option)
  window.addEventListener("resize",function () {//添加一个单击事件（单击时触发function函数）  
      myChart.resize();
    })
  }
}

</script>
<style scoped>
 .chartBox>div{display: inline-block;float: left;}
.nohaveData{text-align: center;padding-top: 16%;}
.nohaveData p {color: #d9ddda;margin: 0;font-size: 12px;}
</style>


