import {Module, State, Action, Getter, Mutation} from 'vue-decorators';
import fetch from '@/helper/fetch'

// codeType 待缓存的码表类型数据
const codeTypeStorage = {}
/**
 * 缓存码表
 * @param {*} codeTypes 
 */
export const cacheCodes = (...codeTypes) => {
  codeTypes.forEach(codeType => {
    codeTypeStorage[codeType] = 1
  })
}

class CodeCacheStorage {
  constructor() {
    this.caches = {}
  }

  get(type, value) {
    if (!checkType(type, this.caches)) return value
    if (typeof value == 'number') return new String(value)
    if (typeof value != 'string') return value
    value = value.trim()
    if (value.length == 0) return value

    const {map = {}, dataSource = []} = this.caches[type]
    if (Object.keys(map).length == 0) {
      dataSource.forEach(({label, value}) => {
        map[value] = label
      })
    }
    const tmp = map[value]
    if (typeof tmp != 'string' && typeof tmp != 'number') return value
    return tmp
  }

  dataSource(type) {
    if (!checkType(type, this.caches)) return []
    const {dataSource = []} = this.caches[type]
    return dataSource
  }
  
}

const checkType = (type, storage) => {
  if (typeof type != 'string') type = ''
  else type = type.trim()
  if (type.length == 0) return false

  if (typeof storage[type] != 'object') return false
  if (typeof storage[type].map != 'object') return false
  if (typeof storage[type].dataSource != 'object' || !storage[type].dataSource instanceof Array) return false

  return true
}

class AuthCacheStorage {
  caches = { perms: [], roles: []}
  innerCaches = { perms: [], roles: []}
  
  isRole(role) {
    if (typeof role != 'string') return false
    return this.caches.roles.indexOf(role) != -1
  }
  isPermission(permission) {
    if (typeof permission != 'string') return false
    return this.caches.perms.indexOf(permission) != -1
  }
}

@Module({namespaced: true})
export default class CodeModule {
  @State codeStorage = new CodeCacheStorage()
  @State authStorage = new AuthCacheStorage()
  @State isLoaded = false

  @Mutation loaded(state) {
    state.isLoaded = true
  }

  @Mutation pushCodes(state, map = {}) {
    let caches = {}
    let dataSource, dataSourceMap
    Object.keys(map).forEach(key => {
      dataSource = map[key].map(({codeInfoValue: value, codeInfoName: label}) => ({label: label + '', value: value + ''}))
      caches[key] = {dataSource, map: {}}
    })
    state.codeStorage.caches = {...state.codeStorage.caches, ...caches}
  }

  @Mutation pushAuthCodes(state, {perms = [], roles = []} = {}) {
    state.authStorage.caches = {perms:[...state.authStorage.innerCaches.perms, ...perms], roles: [...state.authStorage.innerCaches.roles, ...roles]}
  }

  @Mutation pushInnerAuthCodes(state, {perms = [], roles = []} = {}) {
    state.authStorage.innerCaches = {perms, roles}
    state.authStorage.caches = {perms: [...perms, ...state.authStorage.caches.perms], roles: [...roles, ...state.authStorage.caches.roles]}
  }

  @Action async run({commit, state, rootState}) {
    if (state.isLoaded && rootState.auth.currentUserProfile != null) return
    
    commit('loaded')

    return Promise.all([
      new Promise(async (resolve, reject) => {
        const rsp = await fetch('/api/cache/codes').setParam('codeTypes', Object.keys(codeTypeStorage)).send()
        if (!rsp.success) return reject(new Error(rsp.message))
        const map = rsp.data
        commit('pushCodes', map)
        resolve()
      }),
      new Promise(async (resolve, reject) => {
        const rsp = await fetch('/api/cache/authCodes').send()
        if (!rsp.success) return reject(new Error(rsp.message))
        const map = rsp.data
        commit('pushAuthCodes', map)
        resolve()
      })
    ])
  }
}




