<template>
  <div class="roleManage contentBox">
    <bread-crumb/>

   <div class="currentTitle">
     当前月风险客户报表
    </div>
    <el-button type="warning" @click="() => $emit('close','11111')" style="float:right;margin-right: 20px;margin-top: -37px;line-height: 10px;">关闭</el-button>

    <div class="container whiteBg">
      <self-grid ref="grid" url="/api/reportMgr/queryRiskDetailList" v-model="queryForm" :multi="false" :autoload="autoload" :checkBox="false">
       <template slot="query" slot-scope="{form, query, reset}"></template>
        <el-table-column prop="custName" label="姓名">
          <!-- <template slot-scope ="{row}">
             {{ row.custName.trim().substring(0,1)+"**"}}
          </template> -->
        </el-table-column>
        <el-table-column prop="custCertNo" label="身份证" width="200">
           <template slot-scope ="{row}">
               {{row.custCertNo.replace( row.custCertNo.substr(6, 8), "****")}}
          </template>
        </el-table-column>
         <el-table-column prop="loanAmt" label="金额"/>
        <el-table-column prop="custTel" label="手机号">
           <template slot-scope ="{row}">
               {{row.custTel.replace( row.custTel.substr(3, 4), "****")}}
          </template>
        </el-table-column>
          <el-table-column prop="mngType" label="管理级别">
             <template slot-scope="{row}">{{codeStorage.get('mngType', row.mngType)}}</template>

         </el-table-column>
        <el-table-column prop="dcsRst" label="管理状态">
             <template slot-scope="{row}">{{codeStorage.get('dcsRst', row.dcsRst)}}</template>

        </el-table-column>
       

        
      </self-grid>


      
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";



//码表
require('@/store/cache').cacheCodes('mngType', 'dcsRst',)
@Component
@InjectComponents({ BreadCrumb,})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
   @Prop({type: Object, required: false, default: () => ({})}) params//接受属性
   @Prop({type: Boolean, required: false, default: true}) autoload

  queryForm = {}


  async setParams(params = {}){
     const {name} = params
    this.queryForm = {...this.queryForm,name}
    this.$refs.grid.setDefaultParams({'queryDate':name}).load()//内置一个值
  }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
.currentTitle{background-color: #fff;padding-left: 20px;border-bottom: 1px solid #ecf0f1;   font-size: 14px;line-height: 42px;}
</style>


