<template>
  <div class="breadCrumb">
    <el-breadcrumb separator="》">
	  <el-breadcrumb-item v-for="(item, index) in $route.meta" :key="index">{{item}}</el-breadcrumb-item>
	</el-breadcrumb>
  </div>
</template>

<script>
import Vue from "vue";
import { Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Action, Mutation } from "vue-decorators";

@Component
export default class BreadCrumb extends Vue {
  
}
</script>


<style>
    .breadCrumb{background-color: #fff;padding-left: 20px;border-bottom: 1px solid #ecf0f1}
    .breadCrumb .el-breadcrumb{font-size: 14px;line-height: 42px;}
</style>






