<template>
   <!-- <div id="riskTrends" style="width: 100%;height: 100%;" ref="riskTrends"></div -->
   <div id="riskTrends"  style="width: 100%;height: 100%;" v-loading="loading" element-loading-text="查询中...">
            <div v-show="visable" style="width: 100%;height: 100%;" ref="riskTrends"/>
            <div class="nohaveData" v-show="!visable">
                  <img src="../assets/custimg/nodata.png"/>
                       <p>暂无数据</p>
            </div>
        </div>
</template>
<script>
import Vue from 'vue'

import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'
import echarts from 'echarts'
@Component
export default class HomePage extends Vue{

    loading =false
    visable =false
 async loadData(){
    this.loading = true
    const rsp = await this.$helper.fetch('/api/index/getWarnTrendsData').setParams({...this.form}).send()
    this.loading = false
    if (!rsp.success) return rsp.success
    this.visable = rsp.data.list.length != 0
    if (!this.visable) return

    const {riskTrends = {}} = this.$refs
    this.$nextTick(() => {
        this.riskTrendsedAccChart(riskTrends,{ title :"", optionsData : rsp } )
    })

}

  @Lifecycle async created() {
        this.loadData()
    }

riskTrendsedAccChart(el, { title = "", optionsData = [], options = [] } = {}) {
    const timeArray =[]//时间
     const numArray =[]//num
     const datas = optionsData.data.list
     for(var i in datas){
        let abc = this.$helper.tools.formatDate(datas[i].busiDate, "YYYY-MM-DD").substring(5).replace('-','.');
        if(abc.substring(0,1) == '0') timeArray.push(abc.substring(1))
        else  timeArray.push(abc)
        numArray.push(datas[i].riskCnt)
     }

    const option = {
    color: ['#43c386'],
    // title: {
    //     text: '人数',
    //     textStyle:{ color:'#c8c8c8',fontSize:'14px',}
    // },
    tooltip: {trigger: 'axis'},
    //legend: { data:['风险']},
    grid: {left: '6%',right: '8%',bottom: '5%',top: '15%',containLabel: true},
  
    xAxis: {
        type: 'category',
        boundaryGap: false,
        axisLine: {
                    lineStyle: {type: 'solid',color: '#cccccc',width:'1'}//color左边线的颜色width:'1'//坐标线的宽度
                },
        data: timeArray
    },
    yAxis: {
        type: 'value',
         axisLine: {
                   lineStyle: {type: 'solid',color:'#cccccc',width:'1'}
               },
    },
    series: [
        { name:'已存在风险',type:'line',
        stack: '总量',
        symbolSize:'10',//圆圈大写
        itemStyle:{
            normal:{
                color: '#2ecc71',
                lineStyle:{ width:'4',}//线条粗细
            }
          
        },
        data:numArray}, 
    ]
  };
   let myChart = echarts.init(document.getElementById("riskTrends"));
  myChart.setOption(option)
  window.addEventListener("resize",function () {//添加一个单击事件（单击时触发function函数）  
      myChart.resize();
    })
  }
}

</script>
<style scoped>
.nohaveData{text-align: center;padding-top: 5%;}
.nohaveData p {color: #d9ddda;margin: 0;}
</style>


