<template>
  <div class="roleManage contentBox">
    <bread-crumb/>
    <div class="container whiteBg">
      <self-grid ref="grid" url="/api/reportMgr/queryRiskList" v-model="queryForm" :multi="false" :checkBox="false" >
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline dateTimeInline" :inline="true" :model="form">
               <el-form-item prop="beginTime" label="时间：">
                    <el-date-picker :editable="false" type="date" placeholder="开始日期"
                     v-model="form.beginTime_" @change="value => form.beginTime = value"  style="width: 100%;"></el-date-picker>
                  </el-form-item>

                <el-form-item >
                    <div>-</div>
                  </el-form-item>
                <el-form-item prop="loginId">
                    <el-date-picker   :editable="false" type="date" placeholder="结束日期" v-model="form.endTime_" @change="value => form.endTime = value" style="width: 100%;"></el-date-picker>
                </el-form-item>


           <el-form-item class="buttonTeam">
            <el-button type="primary" @click="query">查询</el-button>
            <!-- <el-button type="danger resetEditClass" @click="reset">重置</el-button> -->
            <!-- <el-button type="success" @click="onSubmit"><i class="iconfont icon-xiazai"></i>下载</el-button> -->
                </el-form-item>
          </el-form>
        </template>
        <el-table-column prop="busiDate" label="时间"/>
        <el-table-column prop="busiTitle" label="名称"/>
         <el-table-column label="操作" width="100">
          <template slot-scope="{row, index}">
            <el-tooltip class="item" effect="light" content="查看"  placement="bottom-start" :visible-arrow="false">
               <a type="primary"  :href="seeReport(row)"  target="_blank">
                <el-button  type="text" class="iconfont icon-fangdajing" size="small"  style="color: #60b8f7;"></el-button>
               </a>
            </el-tooltip>
          </template>
        </el-table-column>

      </self-grid>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";

import addUserForm from './addUserForm'
import editUserForm from './editUserForm'
import resetUserPwdForm from './resetUserPwdForm'

//码表
require('@/store/cache').cacheCodes('billType', 'roleType','status')
@Component
@InjectComponents({ BreadCrumb,addUserForm,editUserForm,resetUserPwdForm})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
  queryForm = {userName: null}
  onSubmit(){

  }
  seeReport(row){
    const inStr = row.busiDate
    return `/api/reportMgr/riskReportDetail/${inStr}`
  }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
    
</style>


