<template>
  <div class="layout">
    <div class="left">
      <div class="logo" @click="() => this.$router.replace({name: 'index'})">
        <img src="../assets/logo.gif" alt="">
      </div>
      <div ref="leftNav" :style="`height: ${layout.innerHeight - 90}px; width: 100%; overflow: hidden;`">
        <el-menu :unique-opened="true" ref="leftNav1" :default-active="defaultActive" :default-openeds="defaultOpeneds" @select="selectResource" theme="dark" style="height: 0; overflow: visbile; width: 100%;">
          <self-left-menu v-for="(menu, index) in resources" :menu="menu" :level="1" :key="index" :active="defaultActive">
            <template slot-scope="{menus=[], level= 1, active}">
              <self-left-menu v-for="(menu, index) in menus" :menu="menu" :level="level" :key="index" :active="active"/>
            </template>
          </self-left-menu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="header">
        <div class="toolbar">
          <el-dropdown class="userLink">
            <span class="el-dropdown-link">
              <span style="display: inline-block;  text-align: right;">{{userName}}</span>
              <i class="iconfont icon-chevron-up"></i>
              <img src="../assets/index/touxiang.png" class="avator" />
            </span>
            <template slot="dropdown">
              <div class="dropdown animated fadeIn" style="margin-top: -10px">
                <img src="../assets/index/topmargin.png" style="width: 100%; position: relative; z-index: 3;"/>
                <ul>
                  <li @click ="logout" ><i class="iconfont icon-kehuguanli_zhuanyifenzu-" style="font-size:18px"></i><span>退出</span></li>
                  <li @click="() => this.modifyLoginPwdFormVisible = true"><i class="iconfont icon-mima"></i><span>修改密码</span></li>
                </ul>
              </div>
            </template>
          </el-dropdown>
        </div>
      </div>
      <div class="content" ref="rightContent" :style="`height: ${layout.innerHeight - 61}px; width: 100%; overflow: hidden; position: relative;`">
        <transition enter-active-class="fadeInDown" leave-active-class="pageHide"><router-view class="animated" :style="`height: 0px;`"  v-if="pageVisbile"/></transition>
      </div>
    </div>

    <self-dialog title="修改密码" v-model="modifyLoginPwdFormVisible">
      <template slot-scope="{close}">
        <modifyloginpwdform :close="close"/>
      </template>
    </self-dialog>

  </div>
</template>

<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter, Mutation, Action} from 'vue-decorators'

import SelfLeftMenu from '@/components/SelfLeftMenu'
import ModifyLoginPwdForm from './ModifyLoginPwdForm'
import Ps from 'perfect-scrollbar'

const reduce = (items, path = []) => {
  return items.forEach((item) => {
      const {children = []} = item
      item.path = [...path]
      reduce(children, [...path, item.index])
  })
}

@Component
@InjectComponents({SelfLeftMenu, "modifyloginpwdform": ModifyLoginPwdForm})
export default class Layout extends Vue {

  @State("auth") auth
  @State("layout") layout
  @Getter('auth/userName') userName
  @Mutation("auth/setCurrentUserProfile") setCurrentUserProfile
  @Action('cache/run') cacheRun

  pageVisbile = false

  modifyLoginPwdFormVisible = false
  resources = []

  defaultActive = null
  defaultOpeneds = []
  firstClick = true

  @Computed pageKey() {
    if (!this.$router.name) return 'default'
    return this.auth.routerKeys[this.$router.name] || 'default'
  }

  @Computed pageStyle() {
    const width = `${this.layout.innerWidth - 300}px`
    const height = `100px`
    return { minWidth:width, minHeight:height, position: 'relative' }
  }

  // @Computed avator() {
  //   return "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1508319072581&di=481a357f553539400e5d6b6811fe2a2e&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2015-01-17%2F221431156.jpg"
  // }

  selectResource(index) {
    if (this.firstClick) {
      this.firstClick = false
      const dom = document.getElementById('defualtSelfLeftNavActiveIndex')
      if (dom) dom.className = "el-submenu"
    }
    if (this.$router.currentRoute.name == index) {
      this.pageVisbile = false
      this.$nextTick(() =>  this.pageVisbile = true)
      return 
    }
    this.defaultActive = index
    this.$nextTick(() => this.$router.push({name: index}))
  }

  async logout() {
    await this.$confirm("确定退出登录?")
    const rsp = await this.$helper.fetch("/api/login/logout").send()
    if (!rsp.success) return
     //this.$notify({title: '提示',message:  rsp.message,type: 'success',duration:4000});
    await this.setCurrentUserProfile(null)
    // setTimeout(() =>  window.location.reload(true), 500)
    this.$router.replace({name: "login"})
  }

  async getResources() {
    const rsp = await this.$helper.fetch('/api/cache/cacheResources').send()
    if (!rsp.success) return
    let {resources = []} = rsp.data
    resources = resources.map(({authCode: index, menuId: id, parentMenuId: pid, menuIcon: icon, menuName: text, actionType: type}) => ({index, id, pid, icon, text, type}))
    
    let routerName = this.$router.currentRoute.name
    let currentResource = null
    // web 有首页
    // if (routerName == 'index') routerName = null
    
    resources.forEach(resc => {
      if (currentResource != null) return false
      const {index, type} = resc
      if (routerName != null) {
        if (routerName != index) return true
        currentResource = resc
        return false
      }

      if ('10012002' != type) return true
      currentResource = resc
      return false
    })
    routerName = (currentResource || {}).index || 'index'
    resources = this.$helper.tools.toTreeArray(resources)
    this.resources = resources
    resources = reduce(resources)
    this.defaultActive = routerName
    this.defaultOpeneds = (currentResource || {}).path || []
    
  }

  @Lifecycle async created() {
    this.$nextTick(() => this.pageVisbile = true)
    await this.getResources()
  }

  @Lifecycle mounted() {
    new Ps(this.$refs.leftNav, {
      wheelSpeed: 2,
      wheelPropagation: false,
      swipePropagation: false,
      minScrollbarLength: 20
    })

    new Ps(this.$refs.rightContent, {
      wheelSpeed: 2,
      wheelPropagation: false,
      swipePropagation: false,
      minScrollbarLength: 20
    })
  }
}
</script>

<style scoped>
.layout { position: absolute; top: 0; left: 0; width: 100%; height: 100%; display: flex; overflow: hidden;}
.layout > .left { width: 200px;min-width: 200px; height: 100%; background-color: #34495e;}
.layout > .left::-webkit-scrollbar { display: none; }
.layout .logo { height: 90px;  cursor: pointer;position: relative;background: #2ecc71}
.layout .logo img{ position: absolute;width: 80%;top: 10%;left: 10%;margin-top: -3px}
.right { width: 100%; height: 100%; }
.header { height: 60px; width: 100%; border-bottom: 1px solid #2ecc71; position: relative; }
.toolbar { position: absolute; top: 0; right: 0; height: 60px; line-height: 60px;}
.content {width: 100%; height: 100%; background-color: #ecf0f1;}
.layout .pageHide {display: none;}
.userLink { margin: 0 40px 0 0; cursor: pointer; position: relative;z-index: 9;}
.userLink .el-dropdown-link {position: relative; right: -15px; }
.userLink .avator { width: 34px; height: 34px; border-radius: 50% 50%; display: inline-block; position: relative; top: 13px;    border: 1px solid #2ecc71;margin-left: 5px;}
.userLink .dropdown { position: absolute; top: 49px; display: none; width: 150px;right:-38px;}
.userLink:hover .dropdown {display: block;}
.userLink:hover .dropdown ul {display: block; width: 118px; margin: 0 auto; padding: 0 5px; background-color: #fff; position: absolute; top: 25px; left: 10px; border-radius: 0 0 6px 6px;border: 1px solid #dfdfdf !important}
.userLink:hover .dropdown ul li {list-style: none; padding: 0; margin: 0; line-height: 35px; border-bottom: 1px solid #dfdfdf; color: #5b646c; display: flex; }
.userLink:hover .dropdown ul li:hover{color: #2ecc71}
.userLink:hover .dropdown ul li:last-child {border-bottom: none;}
.userLink:hover .dropdown ul li i {display: inline-block; color: #2ecc71; width: 40px; text-align: center; font-size: 16px;}
.userLink:hover .dropdown ul li > span {flex-grow: 2;}

/* yw */
.content{overflow: scroll;}
.content::-webkit-scrollbar {
display: none;
}
.el-menu--dark .el-submenu__title, .el-menu--dark .el-submenu__title i{color: #95a5a6;}

</style>





