<template>
  <div class="accManage contentBox mySwitch">
    <bread-crumb/>
    <div class="container whiteBg">
      <self-grid ref="grid" url="/api/userMgr/selectUserList" v-model="queryForm" :multi="false" >
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline" :inline="true" :model="form">
          
            </el-form-item>
            <el-form-item class="buttonTeam">
             
              <el-button type="primary" @click="add">添加</el-button>
            </el-form-item>
          </el-form>
        </template>
            <el-table-column prop="loginId" label="账号"/>
        <el-table-column prop="userName" label="姓名"/>
        <el-table-column prop="userDept" label="部门"/>
         <el-table-column prop="status" label="状态">
            <template slot-scope="{row}">{{codeStorage.get('status', row.status)}}</template>
         </el-table-column>
        <el-table-column prop="loginTime" label="上次登录时间">
             <template slot-scope="{row,index}">{{$helper.tools.formatDate(row.loginTime, "YYYY-MM-DD")}}</template>
        </el-table-column>
        <el-table-column label="操作" width="220" >
          <template slot-scope="{row, index}">
            <el-switch on-text="启用" off-text="禁用" on-value="00001001" off-value="00001002" v-model="row.status" @change="value => del(row, value)"/>
            <el-tooltip class="item" effect="light" content="客户转移"  placement="bottom-start" :visible-arrow="false">
              <el-button  type="text" class="iconfont icon-kehuguanli_zhuanyifenzu-"  @click.stop="userMove(row)" style="color: #f3b760;" ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="light" content="编辑"  placement="bottom-start" :visible-arrow="false">
              <el-button  type="text" class="iconfont icon-bianji" size="small" @click.stop="userEdit(row)" style="color: #2ecc71;" ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="light" content="重置密码"  placement="bottom-start" :visible-arrow="false">
               <el-button  type="text" class="iconfont icon-mima" size="small" @click.stop="resetPwd(row)" style="color: #60b8f7;"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </self-grid>
    </div>
    <self-dialog title="新增账户" v-model="addUserFormVisible" @close="closeAddUserForm">
      <template slot-scope="{close}">
        <add-user-form :close="close"/>
      </template>
    </self-dialog>

     <self-dialog ref="editUserForm" title="编辑账户" v-model="editUserFormVisible" @close="closeEditUserForm">
      <template slot-scope="{close, params}">
        <edit-user-form :close="close" :params="params"/>
      </template>
    </self-dialog>


    <self-dialog ref="resetUserPwdForm" title="重置密码" v-model="resetPwdFormVisible" @close="closeResetUserPwdForm">
      <template slot-scope="{close, params}">
        <reset-user-pwd-form :close="close" :params="params"/>
      </template>
    </self-dialog>

  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";

import addUserForm from './addUserForm'
import editUserForm from './editUserForm'
import resetUserPwdForm from './resetUserPwdForm'

//码表
require('@/store/cache').cacheCodes('billType', 'roleType','status')
@Component
@InjectComponents({ BreadCrumb,addUserForm,editUserForm,resetUserPwdForm})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
  queryForm = {}
  addUserFormVisible = false
  editUserFormVisible = false;
  resetPwdFormVisible = false;
  value3 = '100'
//用户添加
  add() {
    this.addUserFormVisible =  true
  }
  closeAddUserForm(){
    this.$refs.grid.load({"pageNo":1});//重新加载列表
  }
  //删除
 async del(row,grantRuleState) {
    const rsp = await this.$helper.fetch('/api/userMgr/updateSysUser').setParams({...row}).send()
       this.$refs.grid.load({"pageNo":1});//重新加载列表
    if (!rsp.success) return
     this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
  }
  //客户转移
  userMove(row) {
     if(row.status =='00001002'){
          this.$notify({title: '提示',message:  '当前用户已失效,请启用',type: 'error',duration:4000});
          return
    }
     this.$emit('open', row)//触发自定义事件open 并把参数传给custmovewrapper中的open
  }
 //编辑
  userEdit(row){
    if(row.status =='00001002'){
          this.$notify({title: '提示',message:  '当前用户已失效,请启用',type: 'error',duration:4000});
          return
    }
    // const {userName = '',loginId =''} = row //从row中解构出userName和loginId
    // this.editUserFormVisible =  true
    // this.$refs.editUserForm.setParams({userName,loginId})
   
    this.editUserFormVisible =  true
    this.$refs.editUserForm.setParams(row)
  }

  closeEditUserForm(){
     this.$refs.grid.load({"pageNo":1});//重新加载列表
  }
  
    async resetPwd(row){
      if(row.status =='00001002'){
        this.$notify({title: '提示',message:  '当前用户已失效,请启用',type: 'error',duration:4000});
        return
      }
      await this.$confirm('此操作将重置该用户密码, 是否继续?', '重置密码')
      const rsp = await this.$helper.fetch("/api/userMgr/resetUserPwd").setParam("userId",row.userId).send()
      if (!rsp.success) return
      this.$refs.grid.load({"pageNo":1});//重新加载列表
      await this.$alert('密码为123456', '重置密码成功')

      // this.$notify({title: '提示',message:  '重置密码成功',type: 'success',duration:4000});
    }


  closeResetUserPwdForm(){
    this.$refs.grid.load({"pageNo":1});//重新加载列表
  }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}

</style>


