<template>
  <div id="network" :style="style" v-loading="loading" element-loading-text="尝试重新恢复连接中..." >
    <!-- <h1>{{layout.innerHeight}}</h1> -->
    <slot></slot>
  </div>
</template>

<script>
import Vue from "vue";
import { Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Action, Mutation } from "vue-decorators"

@Component
export default class Network extends Vue {
  online = false
  loading = false

  @State("auth") auth
  @State("layout") layout
  @Mutation("auth/setCurrentUserProfile") setCurrentUserProfile
  @Action("layout/run") layoutRun
  @Action('cache/run') cacheRun


  showError = false
  errorRsps = []
  

  @Computed style() {
    const width = `${this.layout.innerWidth}px`
    const height = `${this.layout.innerHeight}px`
    return { width, height }
  }

  async getCrypto() {
    // 获取RSA非对称加密公匙
    const error = async () => {
      this.loading = false
      await this.$alert("连接已断开", '确定尝试重新恢复连接？', {
        callback: () => {
        this.loading = true  
        setTimeout(this.network, 700)
      }})
      // this.loading = true
      
      // await this.network()
      return false
    }
    const rsp = await this.$helper.fetch('/api/login/getCrypto').closeCrypto().onError(error).send()
    if (!rsp.success) return
    if (this.loading) this.loading = false
    const {exponent, modulus} = rsp.data
    this.$helper.fetch.registerCrypt(exponent, modulus)
  }

  async getLoginStatus() {
    // 获取登录状态信息
    const rsp = await this.$helper.fetch('/api/login/getLoginStatus').send()
    if (!rsp.success) return
    
    const {loginStatus = false, currentUserProfile} = rsp.data
    await this.setCurrentUserProfile(currentUserProfile)
     // 登录状态时 码表缓存
    if (loginStatus) await this.cacheRun()
    return loginStatus
  }

  async network() {
    await this.getCrypto()
    
    const loginStatus = await this.getLoginStatus()

    // 更改连接
    this.online = true

    const {currentRouterName = 'index'} = this.auth
    // console.log(currentRouterName)
    if (!loginStatus) this.$router.replace({name: "login"})
    else this.$router.replace({name: currentRouterName})
  }

  // 处理第一次无效签名
  async doCryptoException({fetch, resolve, reject}) {
    await this.getCrypto()
    const rsp = await fetch.send()
    resolve(rsp)
  }

  @Lifecycle async created() {
    const doGlobalError = () => {
      if (this.errorRsps.length == 0) return this.showError = false
      this.showError = true
      const {status, message} = this.errorRsps.shift()
      this.$alert(message, "温馨提示", {
        callback: () => {
          if (status != 403) return doGlobalError()
          this.setCurrentUserProfile(null)
          this.errorRsps = []
          this.$router.replace({name: 'login'})
          this.showError = false
          return
        }
      })
    }

    this.$helper.fetch.onError(({status = 500, message = '服务异常'}, opts) => {
      if (status == 513 && opts.fetch.enableReSend) return this.doCryptoException(opts), true
      this.errorRsps.push({status, message})
      if (this.showError) return false
      doGlobalError()
      return false
    })

    await Promise.all([ this.layoutRun(), this.network() ])
  }

}
</script>

<style scoped>
body{margin: 0}
#network { font-family: 'Avenir', Helvetica, Arial, sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; color: #2c3e50; width: 100%; height: 100%;}
</style>
<style>
.full {width: 100%; height: 100%;}
</style>




