<template>
  <label class="el-checkbox" @click.stop.prevent="__onChecked"><span :class="classCss"><span class="el-checkbox__inner"></span><input type="checkbox" class="el-checkbox__original" value=""></span><span style="color: #6b7074"> 全选</span></label>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'

@Component
export default class GridSelectionCol extends Vue {
  @Prop({type: Boolean, required: false, default: false}) value
  @Prop({type: Boolean, required: false, default: false}) disabled
  
  __onChecked() {
    if (this.disabled) return
    this.$emit('input', !this.value)
  }

  @Computed classCss(){
    return `el-checkbox__input ${this.value ? 'is-checked' : ''} ${this.disabled ? 'is-disabled': ''}`
  }

  @Lifecycle created() {
    console.log(this.$props.disabled, this.classCss)
  }
}
</script>



