import RSAUtils from './security';
const CryptoJS = require('./crypto/crypto-js').CryptoJS
import tools from '../tools';

let CryptStorage = {
  RSAModulus: null,
  RSAPublicExponentVar: null,
  RSAKeyPairVar: null
};



export function registerRSA(exponent, modulus) {
  CryptStorage.RSAModulus = modulus;
  CryptStorage.RSAPublicExponentVar = exponent;
  CryptStorage.RSAKeyPairVar = new RSAUtils.getKeyPair(exponent, "", modulus);
  return CryptStorage.RSAKeyPairVar;
}

export function encryptRSA(data) {
  let reverseData = data.split('').reverse().join('');
  return RSAUtils.encryptedString(CryptStorage.RSAKeyPairVar, reverseData);
}

export function encryptAES(data, rule) {
  let key = CryptoJS.enc.Utf8.parse(rule);
  let iv = CryptoJS.enc.Utf8.parse(rule);
  let encrypted = CryptoJS.AES.encrypt(data, key,
    {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    });
  return encrypted.toString();
}

export function decryptAES(data, rule) {
  let key = CryptoJS.enc.Utf8.parse(rule);
  let iv = CryptoJS.enc.Utf8.parse(rule);
  let decrypted = CryptoJS.AES.decrypt(data, key,
    {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    });
  return decrypted.toString(CryptoJS.enc.Utf8);
}

export function enable() {
  return CryptStorage.RSAKeyPairVar != null;
}

export function encrypt(data) {
  if (!CryptStorage.RSAKeyPairVar) throw new Error("请配置加密公匙");
  let rule = tools.getUUID(16);
  let cryptRule = encryptRSA('' + rule);
  let cryptData = encryptAES(data, rule);
  return {rule, cryptRule, cryptData};
}

export function decrypt(data, rule) {
  if (!CryptStorage.RSAKeyPairVar) throw new Error("请配置加密公匙");
  if (data == null) return '';
  if (typeof data !== 'string' && data.length == 0) return '';
  return decryptAES(data, rule);
}

export const Crypt = {register: registerRSA, encrypt, decrypt, enable};
export default Crypt;




