<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="转移客户:" prop="userIdsLength">
      <div>{{form.userIdsLength}}</div>
    </el-form-item>
    <el-form-item label="转移到:"  prop="newUserId" class="custMoveAllRoller">
      <el-select v-model="form.newUserId" clearable placeholder="请选择">
        <el-option v-for="(item, index) in allRole" :key="index" :label="item.userName" :value="item.userId"/>
      </el-select>
    </el-form-item>
    <el-row>
      <el-col :span="12" style="text-align: right;"><el-button @click="close">取 消</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success" @click="submit" style="float:left;color: #fdf7f7" :loading="submitLoding">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'

//码表
require('@/store/cache').cacheCodes('billType', 'roleType')
@Component
export default class ModifyLoginPwdForm extends Vue{
 //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @Getter('auth/userName') userName
  @Prop({type: Function, required: true, default: null}) close
  @Prop({type: Object, required: false, default: () => ({})}) params//接受属性
  form = {loginId: '', newUserId: '',}
  submitLoding = false
  rules = {
    newUserId: [
      {trigger: 'change', required: true, message: '请选择人员'}
    ],
  }
  async submit() {
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
    //oldUserId 是从一个页面来 newUserId选择中的值 custIds 第二个页面选择的值
    const {userId: oldUserId, newUserId, userIds: custIds} = this.form
    const formData = {oldUserId, newUserId, custIds}
    // const newForms = {"oldUserId":this.form.userId,"newUserId":this.form.newUserId,"custIds":this.from.userIds}
   this.submitLoding = true
   const rsp = await this.$helper.fetch('/api/custMgr/custTransfer').setParams(formData).send()
   this.submitLoding =false
    if (!rsp.success) return rsp.success
    this.close(Math.random())
  }
   // 查询所有客户
  allRole = []
  async queryAllRole(){
       const newForms = {"userId":this.form.userId,"userPwd":this.form.userId}
      const rsp = await this.$helper.fetch("/api/userMgr/queryCustManger").setParams(newForms).send()
      if (!rsp.success) return
      this.allRole = rsp.data.list
  }
  @Lifecycle created() {
    const {rows = [], userId} = this.params
    const userIds = rows.map(({custId}) => custId)
    const userIdsLength = userIds.length
    this.form = {...this.form, userIds, userId,userIdsLength}
    this.queryAllRole()
  }


}
</script>




