<template>
  <div class="full">
    <template v-if="visible" >
        <!-- // 父组件 -->
        <acc-manage ref="accManage" @open="open"/>
        <!-- <self-grid ref="page0" @open.stop="open"></self-grid> -->
    </template>
    <template v-if="!visible">
        <!-- // 子组件 -->
          <cust-move ref="custMoveForm" @close="close" :autoload="false"/>
        <!-- <self-grid ref="page1" @close.stop="close"></self-grid> -->
    </template>
  </div>
</template>

<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";

import custMove from './custMove'
import accManage from './accManage'


@Component
@InjectComponents({custMove,accManage})
export default class ModifyLoginPwdForm extends Vue{
    visible = true
    //获取accmanage中的emit传过来的参数
    open(params) { 
        this.visible = false
      this.$nextTick(() => {
        this.$refs.custMoveForm.setParams(params)
      })
    }

    close(data) {
        this.visible = true
        //this.$refs.accManage.load(data)
    }


}
</script>




