<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="姓名:" prop="custName">
      <el-input v-model="form.custName" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="身份证:" prop="custCertNo">
        <el-input v-model="form.custCertNo" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="手机号:" prop="custTel">
        <el-input v-model="form.custTel" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="借款金额:" prop="loanAmt">
        <el-input v-model="form.loanAmt" auto-complete="off"></el-input>
    </el-form-item>
    <el-row>
      <el-col :span="12" style="text-align: right;"><el-button @click="close">取 消</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success" @click="submit" style="float:left;color: #fdf7f7" :loading= "submitLoading">确 定</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'

@Component
export default class ModifyLoginPwdForm extends Vue{

  @Getter('auth/userName') userName

  @Prop({type: Object, required: false, default: () => ({})}) params//接受父页面的传过来的值
  @Prop({type: Function, required: true, default: null}) close//接受父页面的传过来close
  submitLoading = false
  form = { custName: '',custCertNo:'',custTel:'',loanAmt:''}
  rules = {
    custName: [
      {trigger: "blur", required: true, message: '请输入姓名'},
      {whitespace: true, message: '不能包含空格'},
      {max: 30, message: '长度不能大于30个字符'}
    ],
    custCertNo: [
      {trigger: "blur", required: true, message: '请输入身份证'},
      {whitespace: true, message: '不能包含空格'},
       {trigger: "blur,change", validator: (rule, value, callback) => {
         if (this.$helper.tools.isIdCard(value)) return callback()
        return callback(new Error("无效身份证"))
      }}
    ],
     custTel: [
      {trigger: "blur", required: true, message: '请输入电话'},
      {whitespace: true, message: '不能包含空格'},
       {trigger: "blur, change", validator: (rule, value, callback) => {
            if (value.length == 11 && /[0-9]{11}/.test(value)) return callback()
            return callback(new Error('请输入长度为11位有效的手机号码'))
         }}
    ],
     loanAmt: [
        {trigger: "blur", required: false, message: '请输入借款金额'},
        {trigger: 'blur, change', max: 9, message: '长度不得超过9个字符' },
        {trigger: "blur, change", validator: (rule, value, callback) => {
          if(!value) return callback()
          // var isNum=/^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
          var isNum =   /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
          if (isNum.test(value)) return callback()
          return callback(new Error('请输入正确的金额'))
        }}
    ]
  }
    
  async submit() {
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//验证
    this.submitLoading = true
     const rsp = await this.$helper.fetch('/api/custMgr/updateCustInfo').setParams({...this.form}).send()
     this.submitLoading = false
      if (!rsp.success) return rsp.success
      this.$notify.success({title: '成功',message: '保存成功',duration:4000});
      this.close()
  }

  @Lifecycle created() {
    let {loanAmt =''} = this.params
    loanAmt = loanAmt.toString()//把loanAmt字段处理为字符
    const newParams = {...this.params,loanAmt}
    this.form = {...this.form, ...newParams}
  }
}
</script>




