<template>
  <div class="roleManage contentBox">

     <div class="breadCrumb">
     <el-breadcrumb separator="》">
            <el-breadcrumb-item>业务管理</el-breadcrumb-item>
            <el-breadcrumb-item >借款人信息</el-breadcrumb-item>
            <el-breadcrumb-item>风险报告列表</el-breadcrumb-item>
            </el-breadcrumb>
            
     </div>
    <div class="container whiteBg">
      <self-grid ref="grid" url="/api/custMgr/queryCustRiskReportlist" v-model="queryForm" :multi="false" :checkBox = "false" :autoload="autoload">
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline dateTimeInline" :inline="true" :model="form" >
       
           <el-form-item prop="beginTime" label="报告时间：">
                    <el-date-picker :editable="false" type="date" placeholder="开始日期"
                     v-model="form.busiDate_" @change="value => form.busiDate = value"  style="width: 100%;"></el-date-picker>
           </el-form-item>

            <el-form-item label="风险状态：">
            <el-select v-model="form.dcsRst" clearable placeholder="请选择">
              <el-option v-for="(item, index) in codeStorage.dataSource('dcsRst')" :key="index" :label="item.label" :value="item.value"/>
           </el-select>
            </el-form-item>
            <el-form-item class="buttonTeam">
            <el-button type="primary" @click="query" >查询</el-button>
            <el-button type="warning" @click="() => $emit('close','11111')" >返回</el-button>

            
            </el-form-item>
          </el-form>
        </template>
      
         <el-table-column prop="busiDate" label="报告时间" width="180">
         
        </el-table-column>
        <el-table-column prop="mngType" label="风控等级" width="100">
              <template slot-scope="{row}">{{codeStorage.get('mngType', row.mngType)}}</template>
        </el-table-column>
        <el-table-column prop="prodName" label="风控报告">
          
 </el-table-column>
   <el-table-column prop="freType" label="频次" width="80">
              <template slot-scope="{row}">{{codeStorage.get('freType', row.freType)}}</template>
        </el-table-column>

        <el-table-column prop="dcsRst" label="风险状态" class-name="stateImg"  width="120">
          <template slot-scope ="{row}">
              <img v-if="row.dcsRst == '10017001'"  src="../assets/custimg/icon_fengxian.png" alt=""> 
              <img v-if="row.dcsRst == '10017002'" src="../assets/custimg/icon_tixing.png" alt=""> 
              <img v-if="row.dcsRst == '10017003'" src="../assets/custimg/icon_anquan.png" alt=""> 
              <img v-if="row.dcsRst == '10017004'" src="../assets/custimg/icon_zanwu.png" alt=""> 
          </template>
        </el-table-column>
  
        <el-table-column label="操作" width="100">
          <template slot-scope="{row, index}">
            <el-tooltip class="item" effect="light" content="查看"  placement="bottom-start" :visible-arrow="false">
                <el-button  type="text" class="iconfont icon-fangdajing" size="small" @click.stop="seeReport(row)" style="color: #60b8f7;"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </self-grid>
    </div>

 <self-dialog ref="historyCustReportForm" title="风险报告" v-model="historyCustReportFormVisible" @close="historyCustReportForm" class="indexDialog">
            <template slot-scope="{close, params}">
                <history-cust-report :close="close" :params="params"/>
            </template>
 </self-dialog>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
import serviceCustModInfoForm from './serviceCustModInfoForm'
import historyCustReport from './historyCustReport'
//码表
require('@/store/cache').cacheCodes('prodStatus', 'dcsRst',"mngType")
@Component
@InjectComponents({ BreadCrumb,serviceCustModInfoForm,historyCustReport})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage

    @Prop({type: Boolean, required: false, default: true}) autoload
  queryForm = {}
  historyCustReportFormVisible =false
    form={toManage:'toManage' }
    historyData ={}


    seeReport(row){
      if(!row.billId) 
       return this.$notify({title: '提示',message:  '暂无报告',type: 'error',duration:4000});
      this.historyCustReportFormVisible =true
      const newD = {...this.historyData,...row }
      this.$refs.historyCustReportForm.setParams(newD)
    }


      historyCustReportForm(closed = false, ...args){
        this.$refs.grid.load({"pageNo":1});//重新加载列表
    }


     async setParams(params = {}){
       const {custId=''} =params.row
       this.historyData = {...params.row}
      this.$refs.grid.setDefaultParams({custId}).load()//内置一个值

  
  }

 
}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}

</style>


