<template>
  <div class="full">
    <template v-if="visible" >
        <!-- // 父组件 -->
        <manage-account ref="manageAccount" @open="open"/>
    </template>
    <template v-if="!visible">
        <!-- // 子组件 -->
          <manage-cust-report  v-if ="isothervis == 'manageCustReport'" ref="manageCustReport" @close="close" :autoload="false"/>
          <cust-choose  v-if ="isothervis == 'custChoose'" ref="custChoose" @close="close" :autoload="false"/>

    </template>
  </div>
</template>

<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";

import manageAccount from './manageAccount'
import manageCustReport from './manageCustReport'
import custChoose from './custChoose'


@Component
@InjectComponents({manageAccount,manageCustReport,custChoose})
export default class ModifyLoginPwdForm extends Vue{
    visible = true
    isothervis =null//显示那个
    open(params ={}) { 
      const {toManage} = params
      this.visible = false
      if(toManage =="toManage"){
            this.isothervis ="custChoose"
            this.$nextTick(() => {
            this.$refs.custChoose.setParams(params)
          })
      }else{
          this.isothervis ="manageCustReport"
            this.$nextTick(() => {
              this.$refs.manageCustReport.setParams(params)
      })
      }

    }

    close(data) {
        this.visible = true
    }


}
</script>




