
const beforeEach = async function(config = {}) {
  const {router, to, from , next, perms = [], roles = []} = config
  const store = router.app.$store
  const app = router.app
  
  if (!store) return setTimeout(async () => beforeEach(config), 0)

  const {loginStatus = false, fetched = false} = store.state.auth
  let name = to.name
  //console.log('1', to.name, from.name, to.meta)
  //if (typeof name != 'string') return next({path: 'unauthorized'})
  if (name != 'login') store.commit('auth/setCurrentRouterName', name)
  if (!fetched) {
    store.commit('cache/pushInnerAuthCodes', {perms, roles})
    //if (to.name != 'login') return next({name: 'login'})
    return
  }
  if (!loginStatus && name != 'login') return next({name: 'login', meta: {currentRouterName: name}})
  if (loginStatus && name == 'login') return next({name: 'index'})
  if (to.fullPath == '/' && to.name != 'index'){
    //console.log(3333,to.name)
    if(to.name =='404' || to.name=="") return next({name: 'index'})
    else return next(to.name)
  } 
  if (!name) return next()
  // console.log(store.state.cache.authStorage.isPermission(name), name)
  if (!store.state.cache.authStorage.isPermission(name)) {
    // app.$message('无权限访问')
    //console.log(app, 'not auth')
    return next({name: 'index'})
  }
  next()
}

export default beforeEach


