<template>
  <div class="roleManage contentBox">
    <bread-crumb/>
    <div class="container whiteBg">
      <self-grid ref="grid" url="/api/custMgr/queryCustMngList" v-model="queryForm" :multi="true">
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline" :inline="true" :model="form" >
           <el-form-item label="姓名：">
              <el-input v-model="form.custName"></el-input>
            </el-form-item>
            <el-form-item label="身份证号码：">
              <el-input  v-model="form.custCertNo"></el-input>
            </el-form-item>
            <el-form-item label="状态：">
            <el-select v-model="form.dcsRst" clearable placeholder="请选择">
              <el-option v-for="(item, index) in codeStorage.dataSource('dcsRst')" :key="index" :label="item.label" :value="item.value"/>
           </el-select>
            </el-form-item>

           <el-form-item label="管理等级：">
            <el-select v-model="form.mngType" clearable placeholder="请选择">
              <el-option v-for="(item, index) in codeStorage.dataSource('mngType')" :key="index" :label="item.label" :value="item.value"/>
           </el-select>
            </el-form-item>

            <el-form-item class="buttonTeam">
            <el-button type="primary" @click="query" >查询</el-button>
              <!-- <el-button type="danger resetEditClass" @click="reset" >重置</el-button> -->
               <el-button type="danger" @click.stop="cancelManage">取消管理</el-button>
              <el-button type="success" @click="toManage">管理</el-button>
            </el-form-item>
          </el-form>
        </template>
        <!-- <el-table-column prop="userId" label="userid" style="display: none;"/> -->
         <el-table-column prop="custName" label="姓名" width="80">
          <!-- <template slot-scope ="{row}">
             {{ row.custName.trim().substring(0,1)+"**"}}
          </template> -->
        </el-table-column>
        <el-table-column prop="custCertNo" label="身份证" width="180">
           <template slot-scope ="{row}">
               {{row.custCertNo.replace( row.custCertNo.substr(6, 8), "****")}}
          </template>
        </el-table-column>
        <el-table-column prop="custTel" label="手机号">
           <template slot-scope ="{row}">
               {{row.custTel.replace( row.custTel.substr(3, 4), "****")}}
          </template>
 </el-table-column>
        <el-table-column prop="dcsRst" label="管理状态" class-name="stateImg"  width="120">
          <template slot-scope ="{row}">
              <img v-if="row.dcsRst == '10017001'"  src="../assets/custimg/icon_fengxian.png" alt=""> 
              <img v-if="row.dcsRst == '10017002'" src="../assets/custimg/icon_tixing.png" alt=""> 
              <img v-if="row.dcsRst == '10017003'" src="../assets/custimg/icon_anquan.png" alt=""> 
              <img v-if="row.dcsRst == '10017004'" src="../assets/custimg/icon_zanwu.png" alt=""> 
          </template>
           <!-- <template slot-scope="{row}">{{codeStorage.get('mngStatus', row.mngStatus)}}</template> -->
        </el-table-column>
        <el-table-column prop="mngType" label="管理等级" width="100">
              <template slot-scope="{row}">{{codeStorage.get('mngType', row.mngType)}}</template>
        </el-table-column>
        <el-table-column prop="prodName" label="风控包" class-name="noImgClass">
            <template slot-scope ="{row}">
                  {{row.prodName}}
                   <img v-if="row.prodStatus == '00001002'"  src="../assets/custimg/img_seal.png" alt=""> 
          </template>
        </el-table-column>
          <el-table-column prop="freType" label="频次" width="80">
              <template slot-scope="{row}">{{codeStorage.get('freType', row.freType)}}</template>
        </el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="{row, index}">
            <el-tooltip class="item" effect="light" content="查看"  placement="bottom-start" :visible-arrow="false">
                <el-button  type="text" class="iconfont icon-fangdajing" size="small" @click.stop="seeReport(row)" style="color: #60b8f7;"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </self-grid>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";
import serviceCustModInfoForm from './serviceCustModInfoForm'

//码表
require('@/store/cache').cacheCodes('prodStatus', 'dcsRst',"mngType")
@Component
@InjectComponents({ BreadCrumb,serviceCustModInfoForm})
export default class AccManage extends Vue {
  //使用码表
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
  queryForm = {}
    form={
      toManage:'toManage'
    }
  
    seeReport(row){
      if(!row.billId) 
       return this.$notify({title: '提示',message:  '暂无报告',type: 'error',duration:4000});
      this.$emit('open',row)
    }

    toManage(){
      const rows = this.$refs.grid.selectedRows
      if(rows.length ==0) 
      return this.$notify({title: '提示',message:  '请至少选择一条数据',type: 'error',duration:4000});
      const dataFrom = {...this.form,rows}
      this.$emit('open',dataFrom)
    }
    //取消管理
  async  cancelManage(){
      const rows = this.$refs.grid.selectedRows
      if(rows.length ==0) 
      return this.$notify({title: '提示',message:  '请至少选择一条数据',type: 'error',duration:4000});
      const arrayrows = []
      for (var i=0;i<rows.length;i++){
          arrayrows.push({'sysUuid': rows[i].sysUuid,'custId':rows[i].custId,'prodId':rows[i].prodId})
        
      }
      await this.$confirm("确定取消管理吗?")
      const rsp = await this.$helper.fetch('/api/custMgr/cancelCustProds').setParam("custProdArray",arrayrows).send()
      if (!rsp.success) return
      this.$refs.grid.load({"pageNo":1});//重新加载列表
      this.$notify({title: '成功',message:  rsp.message,type: 'success',duration:4000});
    }
}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}

</style>


