<template>
  <el-dialog :title="title" :visible="value" :before-close="beforeClose" :close-on-click-modal="false">
    <slot :close="beforeClose" v-if="visible" :params="params"/>
  </el-dialog>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'

@Component
export default class Dialog extends Vue {
  @Prop({type: Boolean, required: true, default: false}) value
  @Prop({type: String, required: true, default: null}) title

  visible = false
  params = {}

  beforeClose(...args) {
    if (args.length == 1 && args[0] instanceof Event) args = []
    this.$emit('input', false)
    this.$emit('close', args.length != 0, ...args)
  }

  setParams(params = {}) {
    this.params = {...this.params, ...params}
  }

  @Watch("value") valueWatch(value) {
    if (!value) return
    if (!this.visible) { this.visible = true; return }

    this.visible = false
    this.$nextTick(() => this.visible = true)
    
  }
}
</script>




